import React, { useState, useContext, useRef, useEffect } from "react";
//style
import "./Body.css";
//components
import Cart from "./Cart/Cart";
import MovieDetail from "./MovieDetail/MovieDetail";
import CardsComponent from "./CardsComonent/CardsComponent";
import Toast from "./Toast/Toast";
import Fab from "./Fab/Fab";
import Route from "./Route/Route";
import Download from "./Download/Download";
import Notify from "./Notify/Notify";
//context
import { routeContext } from "./../../Context/routeContext";
import { layoutContext } from "./../../Context/layoutContext";
import movieContext from "./../../Context/movieContext";
import cartContext from "./../../Context/cartContext";
import SearchResult from "./SearchResult/SearchResult";
//functions
import { filterCartList, removeOneItem, cartListStore } from "./Cart/Func";
import themeContext from "../../Context/themeContext";
import MoreApps from "./MoreApps/MoreApps";
import BoxOffice from "./BoxOffice/BoxOffice";
//
let scroll = 0;
let lastScroll = 0;
function Body({ cart, setCart, changeDismiss, dismiss, headerDismiss, checkKey }) {
  //contexts
  const { route, changeRoute } = useContext(routeContext);
  const { layout } = useContext(layoutContext);
  const { theme } = useContext(themeContext);
  //states
  const [cartList, setCartList] = useState(cartListStore());
  const [toast, setToast] = useState({});
  const [movie, setMovie] = useState(null);
  const [more, setMore] = useState(0);
  const [canI, setCanI] = useState(false);
  const [settingsData, setSettingsData] = useState({
    subtitles: { state: true, languge: "english" },
    quality: 360,
  });
  const [lock, setLock] = useState(false);
  const mainCpt = useRef(null);

  //
  const removeToast = () => {
    setToast({
      state: false,
      type: toast.type,
      text: toast.text,
      func: toast.func,
    });
  };
  const changeToast = (text, type = "warnning", func = false) => {
    setToast({ state: true, type, text, func });
    setTimeout(removeToast, 5000);
  };
  const removeCart = () => {
    setCart(false);
  };
  const saveCartList = (list) => {
    setCartList(list);
    localStorage.cartList = JSON.stringify({
      list,
      time: new Date().getTime(),
    });
  };
  const addCartList = (value) => {
    let submission = true;
    cartList.map((item) => {
      if (item.id === value.id) {
        submission = false;
        return false;
      }
      return true;
    });
    if (submission) {
      saveCartList([
        ...cartList,
        {
          count: cartList.length + 1,
          id: value.id,
          name: value.name,
          price: value.price,
          size: value.size,
          ep: value.ep,
        },
      ]);
    }
  };
  const removeCartList = (id, toast = true, update = false) => {
    if (Array.isArray(id)) {
      saveCartList(filterCartList(id, cartList));
    } else {
      if (update) {
        let fakeList = removeOneItem(id, cartList);
        if (update.name) {
          saveCartList([
            ...fakeList,
            {
              count: fakeList.length + 1,
              id: update.id,
              name: update.name,
              price: update.price,
              size: update.size,
              ep: update.ep,
            },
          ]);
        } else {
          saveCartList([
            ...fakeList,
            {
              count: fakeList.length + 1,
              id: update.id,
              name: update.title,
              price: update.price,
              size: update.size,
              ep: update.ep,
            },
          ]);
        }
      } else {
        saveCartList(removeOneItem(id, cartList));
      }
    }
    if (toast) {
      changeToast("The Movie Is Removed!");
    }
    if (cartList === []) {
      changeDismiss();
    }
  };
  const changeCartList = (value, type, toast) => {
    if (type !== "remove") {
      if (value.ep) {
        removeCartList(value.id, false, value);
      } else {
        addCartList(value);
      }
    } else {
      removeCartList(value);
    }
    if (toast) {
      changeToast(`The Movie Is Added to cart`);
    }
  };
  const goTop = (val = 0) => {
    if (val === 0) {
      lastScroll = scroll;
    }
    mainCpt.current.scrollTop = val;
  };
  const position = () => {
    return scroll;
  };
  let mount = true;

  useEffect(() => {
    if (mount) {
      if (route !== "movie") {
        goTop();
      }
    }

    // eslint-disable-next-line
    return () => mount = false;
  }, [mount, route]);

  function seeMore() {
    let full = mainCpt.current.scrollHeight;
    let sld = position() + 593;
    if (full - sld < 500) {
      if (!canI) {
        setCanI(true);
        setMore(more + 1);
      }
    } else {
      if (canI) {
        setCanI(false);
      }
    }
  }
  return (
    <main
      className="mdl-layout__content"
      ref={!lock ? mainCpt : null}
      onScroll={(e) => {
        scroll = e.nativeEvent.target.scrollTop;
        seeMore();
      }}
      style={theme.bg}
    >
      {checkKey.key ?
        <movieContext.Provider value={{ movie, setMovie }}>
          <div className="page-content">
            <cartContext.Provider
              value={{
                cartList,
                changeCartList,
                saveCartList,
                removeCartList,
                settingsData,
                setSettingsData,
              }}
            >
              <div
                className={`grid-container ${layout} ${lock && "lock"}`}
                onClick={() => changeDismiss()}
                ref={lock ? mainCpt : null}
              >
                <Route
                  components={{
                    movie: () => (
                      <CardsComponent
                        goTop={goTop}
                        lastScroll={lastScroll}
                        more={more}
                        dismiss={dismiss}
                        changeDismiss={changeDismiss}
                        headerDismiss={headerDismiss}
                      />
                    ),
                    "movie detail": () => <MovieDetail headerDismiss={headerDismiss} />,
                    search: () => <SearchResult />,
                    Notifications: () => <Notify />,
                    "more apps": () => <MoreApps />,
                    download: () => <Download />,
                    "box office": () => <BoxOffice />
                  }}
                />

                {route === "cart"
                  ? !cart
                    ? setCart(true)
                    : ""
                  : cart
                    ? setCart(false)
                    : ""}
              </div>
              <Cart
                removeCart={removeCart}
                visibility={cart ? `show` : ``}
                dismiss={dismiss}
                changeToast={changeToast}
                setLock={setLock}
                lock={lock}
              />
              <Fab
                goTop={goTop}
                position={position}
                changeRoute={changeRoute}
                cart={cart}
                lock={lock}
              />
            </cartContext.Provider>
            <Toast toast={toast} setToast={setToast} />
          </div>
        </movieContext.Provider>
        : checkKey.getKey()}
    </main>
  );
}

export default Body;
