import React, { useContext, useState } from "react";
import themeContext from "../../../Context/themeContext";

import "./MoreApps.css";

function MoreApps() {
  const [android, setAndroid] = useState(false);
  const [windows, setWindows] = useState(false);
  const {theme} = useContext(themeContext);

  return (
    <div className="more-apps">
      <div className="con">
        <a href={`http://${window.endPoint}/movie.apk`} download>
          <div
            className={`link a ${android && "enter"}`}
            onMouseEnter={() => setAndroid(true)}
            onMouseLeave={() => setAndroid(false)}
            style={theme.item}
          >
            <i className="material-icons">android</i>
            <div className="text">
              <div className="head">Android</div>
              <div className="des">
                Download the android app(.apk) for free.
              </div>
            </div>
          </div>
        </a>
        <a href={`http://${window.endPoint}/movie.exe`} download>
          <div
            className="link w"
            onMouseEnter={() => setWindows(true)}
            onMouseLeave={() => setWindows(false)}
            style={theme.item}
          >
            <i>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 448 512"
                width="40"
                height="40"
              >
                <path
                  fill={`${!windows ? "rgb(0, 117, 185)" : "white"}`}
                  d="M0 93.7l183.6-25.3v177.4H0V93.7zm0 324.6l183.6 25.3V268.4H0v149.9zm203.8 28L448 480V268.4H203.8v177.9zm0-380.6v180.1H448V32L203.8 65.7z"
                />
              </svg>
            </i>
            <div className="text">
              <div className="head">Windows desktop</div>
              <div className="des">
                Download the windows desktop setup(.exe) for free.
              </div>
            </div>
          </div>
        </a>
      </div>
    </div>
  );
}

export default MoreApps;
