export function formatSize(size,number=false,unit=false){
    let scale = 'MB';
    let result = 0;
    if(size > 999){
        scale = 'GB';
        let tempSize = size/1024;
        if(Math.floor(tempSize) - tempSize){
           let t = tempSize.toString().split('.');
           if(Number(t[1].slice(0,1))){
               result = t[0] + '.' + t[1].slice(0,1);
            }else{
                result = t[0];
            }
        }else{
            result = tempSize;
        }
    }else{
        if(size > 99){
            if(size.toString().search('.') + 1){
                let l = size.toString().split('.');
                result = l[0];
             }else{
                 result = size;
             }  
        }else{
             if(Math.floor(size) - size){
                let l = size.toString().split('.');
                if(Number(l[1].slice(0,1))){
                    result = l[0] + '.' + l[1].slice(0,1);
                }else{
                    result = l[0];
                }
             }else{
                 result = size;
             } 
        }
        if(size < 0.9){
            scale = 'KB';
            let smallSize = size * 1024;
            if(smallSize > 99){
                if(smallSize.toString().search('.') + 1){
                    let l = smallSize.toString().split('.');
                    result = l[0];
                 }else{
                     result = smallSize;
                 }  
            }else{
                 if(Math.floor(smallSize) - smallSize){
                    let l = smallSize.toString().split('.');
                    if(Number(l[1].slice(0,1))){
                        result = l[0] + '.' + l[1].slice(0,1);
                    }else{
                        result = l[0];
                    }
                 }else{
                     result = smallSize;
                 } 
            }
        }
    }
    if(number){
        return result;
    }
    if(unit){
        return scale;
    }
    return result + ' ' + scale
}
export function formatPrice(price){
    if(price > 1000){
        price = price.toString();
        return price.slice(0,-3)+','+price.slice(-3);
    }
    return price;
}