import React, { Fragment,lazy, Suspense, useContext } from "react";
import themeContext from "../../../../Context/themeContext";
//components
import CardDetail from "./CardDetail/CardDetail";
import CardSeriesDetail from "./CardSeriesDetail/CardSeriesDetail";
const Img = lazy(() => import("./Img"));

// import { url } from "inspector";

//
function CardBox({ item, cardState, setCardState, highLightFor }) {
  const { theme } = useContext(themeContext);
  //
  const { movie, price, size } = item;
  let tempTitle;
  const { id, title, rating, plot } = movie;
  if (highLightFor) {
    tempTitle = title.toLowerCase();
    let separate = tempTitle.split(highLightFor.toLowerCase());
    let newTitle = [<Fragment>{separate[0] && separate[0]}</Fragment>];
    let length = separate.length;
    //
    for (var i = 1; i < length; i++) {
      newTitle.push(
        <Fragment>
          <b>{highLightFor.toLowerCase()}</b>
          {separate[i] && separate[i]}
        </Fragment>
      );
    }
    tempTitle = (
      <Fragment>
        {newTitle.map((val, key) => (
          <Fragment key={key}>{val}</Fragment>
        ))}
      </Fragment>
    );
  }
  //
  const smallDetails = { price, size };

  let starUp = Math.round(rating / 2);
  let starDown = Math.floor(rating / 2);
  let starNode = [];
  let starDim = [];
  let starHalf = true;
  for (let starDI = starUp + 1; starDI <= 5; starDI++) {
    starDim.push(starDI);
  }
  for (let starI = 0; starI < starUp - 1; starI++) {
    starNode.push(starI);
  }
  if (starDown === starUp) {
    starHalf = false;
  }
  return (
    <div>
      {cardState ? (
        <div className={`detail-con ${cardState ? "" : "detail-disable"}`}>
          {Number(movie.single) ? (
            <CardDetail name={title} details={smallDetails} id={id} />
          ) : (
            <CardSeriesDetail item={item} setCardState={setCardState} />
          )}
        </div>
      ) : (
        ""
      )}
      <div className="poster" style={theme.imgBg}>
        <div className="waiting">
          <i className="material-icons">access_time</i>
        </div>
        <Suspense fallback={<div></div>}>
          <Img id={id} />
        </Suspense>
      </div>
      <div className="name">
        <span className="actual">{tempTitle?tempTitle:title}</span>
        {Number(movie.single) ? (
          ""
        ) : (
          <div className="s">
            <b>s</b>
          </div>
        )}
        <div className="plot">{plot}</div>
      </div>
      <div className="rate">
        <div className="star">
          {starNode.map((star) => (
            <i className="material-icons light" key={star}>
              star
            </i>
          ))}
          <i className="material-icons light">
            {starHalf ? `star_half` : `star`}
          </i>
          {starDim.map((i) => (
            <i className="material-icons" key={i}>
              star
            </i>
          ))}
        </div>
        <div className="num" style={theme.secBtn}>
          {rating}
        </div>
      </div>
    </div>
  );
}

export default CardBox;
