import React, { useEffect, useState } from "react";
import "./Please.css";
//
function Please({ reRequest, pause, stop, never }) {
  const [seconds, setSeconds] = useState(stop ? 5 * 60 : 10);
  let mount = true;

  useEffect(() => {
    if (mount) {
      let start = 0;
      if (stop) {
        if (mount) {

          setSeconds(
            Math.floor((stop.time + 5 * 60 - new Date().getTime()) / 1000)
          );
        }
        start = stop.time;
      } else {
        start = new Date().getTime();
      }
      const interval = setInterval(() => {
        const diff = seconds - Math.floor((new Date().getTime() - start) / 1000);
        if (diff >= 0) {
          if (mount) {
            setSeconds(diff);
          }
        } else {
          closeInterval();
        }
      }, 1000);
      function closeInterval() {
        clearInterval(interval);
      }

      // eslint-disable-next-line
        return () => { clearInterval(interval); mount = false };
    }
  }, [mount]);
  if (never) {
    return (
      <div className="please">
        <div className="cover" />
        <div className="please-con">
          <div>
            <i className="material-icons">block</i>
            <div className="some-text">
              You can't use this app! You are BANNED!!
            </div>
          </div>
        </div>
      </div>
    );
  }
  if (reRequest || pause || stop) {
    return (
      <div className="please">
        <div className="cover" />
        <div className="please-con">
          {stop ? (
            <div>
              <i className="material-icons">block</i>
              <div className="some-text">You have been banned!</div>
            </div>
          ) : reRequest ? (
            <div>
              <i className="material-icons">signal_wifi_off</i>
              <div className="some-text">
                There was some problem with the server or the network!
              </div>
            </div>
          ) : pause ? (
            <div>
              <i className="material-icons">pause_circle_filled</i>
              <div className="some-text">
                Your activity is paused! Wait until admin starts it!
              </div>
            </div>
          ) : (
                  ""
                )}

          <div className="action">
            <button
              className="mdl-button mdl-js-button mdl-js-ripple-effect"
              onClick={() => {
                if (seconds <= 0) {
                  window.location.assign(window.location.pathname);
                }
              }}
            >
              <i className="material-icons">refresh</i>
              try again {seconds > 0 ? "in" : ""}{" "}
              {stop ? (
                seconds > 0 ? (
                  <span>
                    {Math.floor(seconds / 60) > 0 ? (
                      <small>{Math.floor(seconds / 60)}m</small>
                    ) : (
                        ""
                      )}{" "}
                    {seconds % 60 > 0 ? <small>{seconds % 60}s</small> : ""}
                  </span>
                ) : (
                    ""
                  )
              ) : seconds > 0 ? (
                <small>{seconds}s</small>
              ) : (
                    ""
                  )}
            </button>
          </div>
        </div>
      </div>
    );
  }
  return null;
}
export default Please;
