import React, { useState, useEffect } from "react";
import { darkTheme } from "./Context/themeContext";
//
function Store({ AppLoad }) {
    const theme = localStorage.theme === "dark" ? darkTheme : {};
    const [stores, setStore] = useState([]);
    const selectStore = (val, node) => {
        if (node.nativeEvent.target.className !== 'material-icons loc' && node.nativeEvent.target.className !== 'mdl-button mdl-js-button mdl-button--icon') {
            localStorage.theme = val.dark?'dark':'light';
            localStorage.store = JSON.stringify({
                id:val.id,
                name:val.name,
                help:val.help,
                dark:val.dark,
                time: new Date().getTime(),
            });
            window.location.assign(window.location.pathname);
        }
    };
    useEffect(() => {
        fetch("http://" + window.endPoint + "/api/getStores?getter=true")
            .then((e) => e.json())
            .then((e) => setStore(e))
            .catch((err) => console.log(err));
    }, []);
    //

    const time = localStorage.store
        ? JSON.parse(localStorage.store).time
        : new Date().getTime() - 1000 * 60 * 31;
    if (!localStorage.store || new Date().getTime() - time > 1000 * 60 * 30) {
        return (
            <div className="store-body" style={theme.bg}>
                <div className="stores">
                    <h4>Choose a store</h4>
                    {stores[0]
                        ? stores.map((val, key) => (
                            <div
                                className="store"
                                key={key}
                                onClick={(e) => selectStore(val, e)}
                                style={theme.item}
                            >
                                <div className="img">
                                    <img
                                        src={`http://${window.endPoint}/storage/storeImg/${val.id}l.png`}
                                        alt="store"
                                    />
                                </div>
                                <div className="text">
                                    <div className="name">{val.name}</div>
                                    <div className="address">{val.address}</div>
                                    <div className="distance">
                                        <a target="_blank" rel="noopener noreferrer" href={"https://www.google.com/maps/place/" + JSON.parse(val.location).lat + ',' + JSON.parse(val.location).long}>

                                            <button className="mdl-button mdl-js-button mdl-button--icon">
                                                <i className="material-icons loc">location_on</i>
                                            </button>
                                        </a>
                                        <div className="store-status">
                                            {val.active ?
                                                (<div className="active status-con"><i className="material-icons">radio_button_checked</i>Active</div>)
                                                :
                                                (<div className="closed status-con"><i className="material-icons">ban</i>Closed</div>)

                                            }
                                        </div>
                                    </div>
                                </div>
                            </div>
                        ))
                        : "Loading stores ..."}
                </div>
            </div>
        );
    } else {
        return <AppLoad />;
    }
}
export default Store;
