import React, { useContext, useEffect, useState } from "react";
//style
import "./CartList.css";
//context
import { routeContext } from "./../../../../Context/routeContext";
import { formatSize } from "../../../../Function";
import movieContext from "../../../../Context/movieContext";
import requestContext from "../../../../Context/requestContext";
import CardSeriesDetail from "../../CardsComonent/Card/CardSeriesDetail/CardSeriesDetail";
import themeContext from "../../../../Context/themeContext";
//
function CartList({
  item,
  tool,
  addSelected,
  removeSelected,
  removeCartList,
  changeRemoved,
  dismiss,
}) {
  const [selected, setSelected] = useState(false);
  const [ShowState, setShowState] = useState(false);
  const [choose, setChoose] = useState(null);
  const { changeRoute } = useContext(routeContext);
  const { setMovie } = useContext(movieContext);
  const { setRequest } = useContext(requestContext);
  const { theme } = useContext(themeContext);
  //
  const clicked = () => {
    if (tool === "select") {
      setSelected(selected ? false : true);
    } else if (tool === "remove") {
      removeCartList(item.id, false);
      changeRemoved();
    }
  };
  if (dismiss && selected) {
    setSelected(false);
  }
  function readyDetail() {
    if (item.ep) {
      setRequest('getSeasons', false, item.id).then(e => {
        if (e) {
          setChoose(<CardSeriesDetail item={{ movie: item, seasons: e }} setShowState={setShowState} />)
        }
      })
    }
  }
  let mount = true;

  useEffect(() => {
    if (mount) {
      readyDetail();
    }
     
    // eslint-disable-next-line
        return () => mount = false;
  }, [mount]);

  return (
    <div
      className={`
      cartlist-con 
      ${tool === "select" ? "checking" : ""} 
      ${tool === "remove" ? "deleting" : ""}
                `}
      onClick={() => {
        selected ? removeSelected(item) : addSelected(item);
        clicked();
      }}
    >
      <div className={`checkbox ${selected ? "check" : ""}`}>
        <i className="material-icons">check</i>
      </div>
      <div
        className="item"
        onClick={() => {
          if (tool === "normal") {
            if (!item.ep) {
              setRequest("movie", {}, { id: item.id, name: item.name }).then((e) => {
                changeRoute("movie detail");
                setMovie(e);
              });
            } else {
              setShowState(true);
            }
          }
        }}
        style={theme.item}
      >
        <span className="num">{item.count}. </span>
        <span className="name">
          {item.name} {item.ep ? <span className="ot"><span className="s"><b>S</b></span>{`(${item.ep.length}e)`}</span> : ''}
        </span>
        <span className="price">{item.price} ETB</span>
        <span className="size">{formatSize(item.size)}</span>
      </div>
      <div className="delete">
        <div className="material-icons">clear</div>
      </div>
      <div>
        {ShowState ? choose : ''}
      </div>
    </div>
  );
}
export default CartList;
