import React, { useContext } from "react";
//style
import "./CardDetail.css";
//context
import { layoutContext } from "./../../../../../Context/layoutContext";
import { routeContext } from "./../../../../../Context/routeContext";
import movieContext from "./../../../../../Context/movieContext";
import cartContext from "./../../../../../Context/cartContext";
//request
import requestContext from "./../../../../../Context/requestContext";
//
import { formatSize } from "../../../../../Function";
function CardDetail({ name, details, id }) {
  const { layout } = useContext(layoutContext);
  const { changeRoute } = useContext(routeContext);
  const { setRequest } = useContext(requestContext);
  const { setMovie } = useContext(movieContext);
  const { cartList, changeCartList } = useContext(cartContext);
  //
  let button = false;
  const width = window.screen.width;
  if (width < 460) {
    button = true;
  }
  if (layout === "grid") {
    button = true;
  }
  if (layout === "column") {
    button = false;
  }
  let state = false;
  cartList.map((item, i) => {
    if (item.name === name) {
      state = true;
    }
    return true;
  });
  const removeCartListDelay = () => {
    changeCartList(id, "remove");
  };
  const addCartListDelay = () => {
    changeCartList({
      id: id,
      name: name,
      price: details.price,
      size: details.size,
    });
  };
  // mdl-button--fab mdl-button--mini-fab mdl-button--colored
  return (
    <div className="card-detail">
      <div className="head">{name}</div>
      <div className="body">
        <div className="price">{details.price} ETB</div>
        <div className="size">
          {formatSize(details.size)}
        </div>
      </div>
      <div className="foot">
        <button
          className={`see-more mdl-button mdl-js-button mdl-js-ripple-effect ${
            button ? "mdl-button--fab mdl-button--mini-fab" : ""
          }`}
          onClick={() => {
            changeRoute("movie detail");
            setRequest("movie", {}, { id }).then((e) => {
              setMovie(e);
            });
          }}
        >
          <i className="material-icons">more</i>
          <span>More Details</span>
        </button>
        {state ? (
          <button
            className={`red-btn add-cart mdl-button mdl-js-button mdl-js-ripple-effect mdl-button--raised mdl-button--accent ${
              button ? "mdl-button--fab mdl-button--mini-fab" : ""
            }`}
            onClick={() => {
              setTimeout(removeCartListDelay, 550);
            }}
          >
            <i className="material-icons">clear</i>
            <span>Remove</span>
          </button>
        ) : (
          <button
            className={`add-cart mdl-button mdl-js-button mdl-js-ripple-effect mdl-button--raised mdl-button--accent ${
              button ? "mdl-button--fab mdl-button--mini-fab" : ""
            }`}
            onClick={() => {
              setTimeout(addCartListDelay, 550);
            }}
          >
            <i className="material-icons">add_shopping_cart</i>
            <span>Add To Cart</span>
          </button>
        )}
      </div>
    </div>
  );
}

export default CardDetail;
