import { useContext } from "react";
import { routeContext } from "../../../Context/routeContext";

function Route({ path, component, components }) {
  const { route } = useContext(routeContext);
  if (components) {
    const func = components[route];
    if (func) {
      return func();
    } else {
      return "";
    }
  } else {
    if (route === path) {
      return component();
    } else {
      return null;
    }
  }
}
export default Route;
