/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from 'react'
import './List.css';
//
function List({ data, theme, type, separate, getCard, more, init }) {
    const [lastItem, setLastItem] = useState(25);
    const [item, setItem] = useState([]);
    const [search, setSearch] = useState('');
    const [sortByAmount, setSortByAmount] = useState(false);

    let mount = true;
    useEffect(() => {
        if (mount) {
            setItem(data.sort((a, b) => {
                var x = a.value.toLowerCase();
                var y = b.value.toLowerCase();
                if (type === 'Time') {
                    if (x > y) { return -1; }
                    if (x < y) { return 1; }
                } else {
                    if (x < y) { return -1; }
                    if (x > y) { return 1; }
                }
                return 0;
            }).slice(0, lastItem));
            // eslint-disable-next-line
            return () => (mount = false);
        }
    }, [mount, data.length]);
    useEffect(() => {
        if (mount) {
            if (init) {
                setLastItem(25);
                setItem([]);
                setSearch('');
            }
            // eslint-disable-next-line
            return () => (mount = false);
        }
    }, [mount, init])
    const filter = (array, func) => {
        const length = array.length;
        let result = [];
        for (let i = 0; i < length; i++) {
            if (func(array[i])) {
                result.push(array[i])
            }
        }
        return result;
    }
    useEffect(() => {
        if (mount) {
            if (mount) {
                if (data[0]) {
                    // console.log(data.slice(lastItem,lastItem + 25));
                    if (search === '') {
                        if (item[0]) {
                            if (sortByAmount) {
                                setItem(item.concat(data.sort((x, y) => {
                                    return y.amount - x.amount;
                                }).slice(lastItem, lastItem + 25)));
                            } else {
                                setItem(item.concat(data.slice(lastItem, lastItem + 25)));
                            }
                        } else {
                            if (sortByAmount) {
                                setItem(item.concat(data.sort((x, y) => {
                                    return y.amount - x.amount;
                                }).slice(0, lastItem)));
                            } else {
                                setItem(data.slice(0, lastItem));
                            }
                        }
                    } else {
                        if (sortByAmount) {
                            setItem(item.concat(filter(data.sort((x, y) => {
                                return y.amount - x.amount;
                            }), val => {
                                return val.value.toLowerCase().search(search.toLowerCase()) + 1;
                            }).slice(lastItem, lastItem + 25)));
                        } else {
                            setItem(item.concat(filter(data,val => {
                                return val.value.toLowerCase().search(search.toLowerCase()) + 1;
                            }).slice(lastItem, lastItem + 25)));
                        }
                    }
                    setLastItem(lastItem + 25);
                }
            }
            // eslint-disable-next-line
            return () => (mount = false);
        }
    }, [mount, more]);

    function handleSearch(e) {
        let text = e.target.value;
        setSearch(text);
        setItem([]);
        setItem(filter(data,val => {
            return val.value.toLowerCase().search(text.toLowerCase()) + 1;
        }).slice(0, 25));
    }
    function handleSort(e) {
        let value = e.target.value;

        if (value === 'amount') {
            setSortByAmount(true);
            setItem([]);
            setItem(data.sort((x, y) => {
                return y.amount - x.amount;
            }).slice(0, 25));
        } else {
            setItem([]);
            const isTime = type === 'Time';
            setItem(data.sort((a, b) => {
                var x = a.value.toLowerCase();
                var y = b.value.toLowerCase();
                if (isTime) {
                    if (x > y) { return -1; }
                    if (x < y) { return 1; }
                } else {
                    if (x < y) { return -1; }
                    if (x > y) { return 1; }
                }
                return 0;
            }).slice(0, 25));
            setSortByAmount(false);
        }
    }
    return (
        <div className="sector">
            <div className="input-con">
                <input type="text" className="item input" style={theme.whiteText} placeholder="Search keyword" value={search} onChange={handleSearch} />
                <select className="select" style={theme.fadedWhiteText} onChange={handleSort}>
                    <option value="name" >Name</option>
                    <option value="amount">Amount</option>
                </select>
            </div>
            {item[0] ? (
                item.map((i, key) => (
                    <React.Fragment key={key}>
                        {type === "Time" && separate(i.value) && (
                            <div className="gutter"></div>
                        )}
                        <div
                            className={`item ${(key + 1) % 2 ? "dark" : "light"}`}
                            onClick={() => getCard(type === 'Time' || type === 'Genre' ? i.value : i.movies, i.value)}
                            style={(key + 1) % 2 ? theme.itemLite : theme.itemLiteDarker}
                        >
                            <i>{key + 1}.</i> {i.value} <span>({i.amount})</span>
                        </div>
                    </React.Fragment>
                ))) : (<div />)}
        </div>
    )
}
export default List;