
function serverPost(whatYouWant, prams = "",key,stringify=false) {
  const storeId = localStorage.store ? `&store=${JSON.parse(localStorage.store).id}` : '';
  const result = fetch(
    `http://${window.endPoint}/api/${whatYouWant}?key=${key?key:localStorage.adminToken}${storeId}`,
    {
        method:'POST',
        body:stringify?prams:JSON.stringify(prams),
      mode: "cors"
    }
  ).then((e)=>e.json()).then(e=>{
    if(e.update !== undefined && e.data){
      return e.data;
    }
    return e
  })
    .catch(err => console.log("ERROR:", err));
  return result;
}

export default serverPost;
