import React, { useContext, useState, useRef } from "react";
//styles
import "./Header.css";
//components
import Tab from "./Tab/Tab";
import Search from "./Search/Search";
import More from "./More/More";
import SearchBox from "./SearchBox/SearchBox";
import Route from "./../Body/Route/Route";
//context
import { routeContext } from "./../../Context/routeContext";
//context
import { layoutContext } from "./../../Context/layoutContext";
import themeContext from "../../Context/themeContext";
//
function Header({ setCart, changeDismiss, dismiss }) {
  const { route, changeRoute } = useContext(routeContext);
  const { changeLayout } = useContext(layoutContext);
  const { theme } = useContext(themeContext);
  //
  const [title, setTitle] = useState("");
  const search = useRef(null);
  let back = "";
  let rotate = "rotate";
  let extra = true;
  if (route === "movie") {
    back = "hide";
    rotate = "";
  } else {
    extra = false;
  }
  
  function check() {
    if (
      search.current.className ===
      "mdl-textfield mdl-js-textfield mdl-textfield--expandable is-upgraded"
    ) {
      setTitle("");
    } else {
      setTimeout(check, 300);
    }
  }
  if (title === "hide") {
    check();
  }
  let docTitle = document.title;
  let headerTitle = docTitle;
  if (docTitle.search("-") + 1) {
    let split = docTitle.split("-");
    if (split[0]) {
      let dir = split[0].trim();
      if (dir === "Movie") {
        headerTitle = split[1];
      } else {
        headerTitle = dir;
      }
    }
  }
  return (
    <header
      className={`mdl-layout__header ${extra ? "" : "hide-extra"} ${route === "search" ? "show-for-search" : ""
        }`}
      onClick={() => changeDismiss()}
      style={theme.header}
    >
      <div className="mdl-layout__header-row">
        <button
          className={`header-back ${back} `}
          onClick={() => {
            setCart(false);
            changeRoute("", true);
          }}
          style={theme.header}
        >
          <i className={`material-icons ${rotate}`}>arrow_back</i>
        </button>
        <span className={`mdl-layout-title title ${title}`}>
          {headerTitle}
        </span>
        <div className="mdl-layout-spacer" />
        {route !== "search" &&
          (<div className="shopping-cart">
            <button
              className="mdl-button mdl-js-button mdl-button--icon"
              onClick={() => changeRoute('cart')}
            >
              <i className='material-icons'>shopping_cart</i>
            </button>
          </div>
          )}
        <Search search={search} setTitle={setTitle} />
        <More changeLayout={changeLayout} />
      </div>
      <div>
        <Route
          path="search"
          component={() => <SearchBox dismiss={dismiss} />}
        />
      </div>
      <Tab eye={route === "movie"} />
    </header>
  );
}

export default Header;
