/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect, useContext } from "react";
//component
import CardsRender from "./CardsRender";
//context
import requestContext from "./../../../Context/requestContext";
import tabContext from "./../../../Context/tabContext";
import loaderContext from "../../../Context/loaderContext";
import Categories from "../Catagories/Categories";
//
function CardsComponent({ goTop, lastScroll, more, dismiss, headerDismiss }) {
  const [items, setItems] = useState([]);
  const [filter, setFilter] = useState(false);
  const [fetching, setFetching] = useState(false);
  const { setRequest } = useContext(requestContext);
  const { setLoading } = useContext(loaderContext);
  const { tab } = useContext(tabContext);

  //
  const recall = (length, append = false) => {
    if (!fetching) {
      setFetching(true);
      const res = setRequest("cardArray", false, { length, filter });
      if (res) {
        res.then((e) => {
          setFetching(false);
          if (e && !e.message) {
            if (append) {
              setItems(items.concat(e))
            } else {
              setItems(e);
              goTop(lastScroll);
            }
          }
        });
      }
    }
    //  else {
    //   setTimeout(recall, 3000);
    // }
  };

  let mount = true;
  useEffect(() => {
    if (mount) {
      if (items[0]) {
        if (tab === 1) {
          recall(items[items.length - 1].ts, true);
        }
        else if (tab === 2) {
          recall(items[items.length - 1].rs, true);
        }
        else if (tab === 3) {
          recall(items[items.length - 1].ps, true);
        }
      }
    }
    // eslint-disable-next-line
        return () => (mount = false);
  }, [mount, more]);
  useEffect(() => {
    if (mount) {
      if (tab <= 3) {
        setLoading(true);
        setItems([]);
        lastScroll = 0;
        recall(0);
        goTop()
        return setLoading(false);
      }
    }
    // eslint-disable-next-line
        return () => (mount = false);
  }, [mount, tab, filter]);
  if (tab <= 3) {
    return (
      <CardsRender
        items={items}
        dismiss={dismiss}
        headerDismiss={headerDismiss}
        filter={filter}
        setFilter={setFilter}
        fetching={fetching}
      />
    );
  } else if (tab === 4) {
    return (
      <Categories
        dismiss={dismiss}
        headerDismiss={headerDismiss}
        filter={filter}
        setFilter={setFilter}
        more={more}
        goTop={goTop}
      />
    );
  } else {
    return "Something went Wrong";
  }
}
export default CardsComponent;
