import React, { useContext } from 'react';
import themeContext from '../../../../Context/themeContext';

function Select({selected,setTool,selectedRemoval,download}) {
    const {theme} = useContext(themeContext);

    return (
        <div>
            <div className="select tool-item">{selected.length} Selected</div>
            <div className="remove tool-item">
                <button
                    className="mdl-button mdl-js-button mdl-js-ripple-effect"
                    onClick={() => download(selected)}
                    style={theme.secBtn}

                >
                    <i className="material-icons">file_download</i> Download
          </button>
            </div>
            <div className="clear tool-item">
                <button
                    className="mdl-button mdl-js-button mdl-js-ripple-effect"
                    onClick={() => selectedRemoval()}
                    style={theme.secBtn}

                >
                    <i className="material-icons">remove_circle_outline</i> Remove
          </button>
            </div>
        </div>
    )
}
export default Select;