import React, { useState, useEffect } from "react";
// import React from "react";
// import Menu from '@material-ui/core/Menu';
// import AppBar from '@material-ui/core/AppBar';
//style
import "./App.css";
//component
import Header from "./Header/Header";
import Body from "./Body/Body";
import Drawer from "./Drawer/Drawer";
import Loader from "./Loader/Loader";
import Please from "./Please/Please";
import Store from "./../Store";
//context
import { layoutContext, defaultLayout } from "./../Context/layoutContext";
import { routeContext, defaultRoute } from "./../Context/routeContext";
import loaderContext from "./../Context/loaderContext";
import searchContext from "./../Context/searchContext";
import tabContext from "./../Context/tabContext";
import requestContext from "./../Context/requestContext";
//request
import serverMan from "./../Request/serverMan";
import serverPost from "../Request/serverPost";
import themeContext, { darkTheme, defaultTheme } from "../Context/themeContext";
///

function App() {
  const [layout, setLayout] = useState(defaultLayout);
  const [cart, setCart] = useState(false);
  const [dismiss, setDismiss] = useState(false);
  const [headerDismiss, setHeaderDismiss] = useState(false);
  const [route, setRoute] = useState(defaultRoute);
  const [history, addHistory] = useState([defaultRoute]);
  const [loading, setLoading] = useState(true);
  const [searchResponse, setSearchResponse] = useState(null);
  const [tab, setTab] = useState(1);
  const [reRequest, setReRequest] = useState(false);
  const [key, setKey] = useState(false);
  const [warn, setWarn] = useState(false);
  const [pause, setPause] = useState(false);
  const [theme, changeTheme] = useState(defaultTheme);
  const [notifyCount, setNotifyCount] = useState(0);
  const [appName] = useState(JSON.parse(localStorage.store).name);
  const [webView, setWebView] = useState(false);
  // const [initialized, setInitialized] = useState(false);


  let mount = false;
  let status = { status: null, time: 0 };
  if (localStorage.status) {
    status = JSON.parse(localStorage.status);
  }
  const setTheme = (val) => {
    val === "dark" && changeTheme(darkTheme);
    val === "light" && changeTheme({});
    localStorage.theme = val;
  };
  // if(localStorage.store){
  //   if(JSON.parse(localStorage.store).dark){
  //     setTheme('dark')
  //   }else{
  //     setTheme('light')
  //   }
  // }
  const changeLayout = (value) => {
    setLayout(value);
    localStorage.layout = value;
  };
  const changeDismiss = () => {
    setDismiss(true);
    setTimeout(() => setDismiss(false), 100);
  };
  const changeHeaderDismiss = () => {
    changeDismiss();
    setHeaderDismiss(true);
    setTimeout(() => setHeaderDismiss(false), 100);
  };
  const changeRoute = (to, back = false) => {

    if (back && history.length > 1) {
      setRoute(history[history.length - 2]);
      history.pop();
      addHistory(history);
    } else if (!back) {
      setRoute(to);
      addHistory([...history, to]);
    }
  };
  useEffect(() => {
    if (webView && window.ReactNativeWebView) {
      window.ReactNativeWebView.postMessage(JSON.stringify({ header: true, value: theme.header ? theme.header.background : '#455a64' }))
    }
  }, [theme.header, webView]);
  useEffect(() => {
    if (!!window.ReactNativeWebView !== webView) {
      setWebView(!!window.ReactNativeWebView)
    }
  }, [webView]);
  const isWebView = !!window.ReactNativeWebView;
  useEffect(() => {
    if (window.ReactNativeWebView) {
      window.ReactNativeWebView.postMessage(JSON.stringify({ route: true, value: route }))
    }
    // eslint-disable-next-line
    return () => mount = false;
  }, [mount, route, isWebView])
  useEffect(() => {
    if (mount) {
      if (isWebView) {
        document.addEventListener('message', e => {
          const data = e.data;
          if (window.ReactNativeWebView) {
            if (data === 'startLinking') {
              setWebView(true)
              window.ReactNativeWebView.postMessage(JSON.stringify({ header: true, value: theme.header ? theme.header.background : '#455a64' }))
            } else if (data === 'back') {
              changeRoute('', true)
              if (history.length === 1) {
                document.title = appName;
              }
            }
          }
        })
        // eslint-disable-next-line
        return () => { document.removeEventListener('message', false); mount = false }
      }
    }
  }, [mount, route, history, appName, theme.header]);
  function shouldStop() {
    if (status.status === "stop") {
      const then = status.time + 5 * 60 * 1000;
      const now = new Date().getTime();
      const remaining = then - now;
      if (remaining > 0) {
        return false;
      }
      return true;
    } else if (status.status === "neverConnect") {
      return false;
    }
    return true;
  }
  let init = false;
  const getKey = () => {
    if (!init) {
      // setTimeout(()=>setInitialized(true),500);
      init = true;
      if (shouldStop()) {
        if (localStorage.token) {
          serverMan("verifyToken", localStorage.token).then((e) => {
            if (e) {
              if (!e.message) {
                if (e.token) {
                  localStorage.token = e.token;
                  setKey(e.token);
                }
              } else {
                if (e.message.indexOf('Suspicious') !== -1) {
                  localStorage.removeItem('token');
                  document.cookie = "laravel_session=; expires=Thu, 01 Jan 1970 00:00:00 UTC";
                  document.cookie = "XSRF-TOKEN=; expires=Thu, 01 Jan 1970 00:00:00 UTC";
                }
                setReRequest(true);
                setLoading(false);
              }
            }
          });
        } else {
          serverMan("init").then((e) => {
            if (e) {
              if (!e.message) {
                if (e.token) {
                  localStorage.token = e.token;
                  setKey(e.token);
                } else {
                  setReRequest(true);
                  setLoading(false);
                }
              } else {
                localStorage.removeItem('token');
                document.cookie = "laravel_session=; expires=Thu, 01 Jan 1970 00:00:00 UTC";
                document.cookie = "XSRF-TOKEN=; expires=Thu, 01 Jan 1970 00:00:00 UTC";
                setReRequest(true);
                setLoading(false);
              }
            }
          });
        }
      }
    }
    return 'Loading...';
  };
  const setPost = (where, what) => {
    setLoading(true);
    return serverPost(where, what, key).then((e) => {
      if (e) {
        setLoading(false);
        return e;
      } else {
        setReRequest(true);
      }
    });
  };
  const setRequest = (where, expect, what = "") => {
    setLoading(true);
    if (shouldStop()) {
      return serverMan(
        where,
        what,
        localStorage.token ? localStorage.token : key,
        tab
      ).then((e) => {
        setLoading(false);
        if (e) {
          if (e.noStore) {
            localStorage.removeItem('store');
            window.location.assign(window.location.pathname);
          }
          if (e.message) {
            setReRequest(true);
            return expect;
          } else {
            //
            if (e.notification - localStorage.notifyCount < 0) {
              localStorage.notifyCount = 0;
            }
            setNotifyCount(e.notification);
            //
            const time = new Date().getTime();
            localStorage.status = JSON.stringify({
              status: e.status,
              time,
            });
            if (e.status === "warn") {
              setWarn(true)
            } else if (e.status === "pause") {
              setPause(true);
            } else if (e.status === "stop" || e.status === "neverConnect") {
              localStorage.removeItem("token");
              window.location.assign(window.location.pathname);
            }
            return e.data;
          }
        } else {
          setReRequest(true);
          return expect;
        }
      });
    }
  };
  useEffect(() => {
    function capitalize(text) {
      const first = text.charAt(0).toUpperCase();
      const other = text.slice(1, text.length);
      return first + other;
    }
    const documentTitle = capitalize(route);
    if (documentTitle === "Movie") {
      document.title = capitalize(appName);
    } else {
      document.title = documentTitle + " - " + capitalize(appName);
    }
    // eslint-disable-next-line
  }, [route, appName]);
  //
  if (status.status === "neverConnect") {
    return (
      <Please reRequest={reRequest} pause={pause} stop={false} never={true} />
    );
  } else if (!shouldStop()) {
    return (
      <Please reRequest={reRequest} pause={pause} stop={status} never={false} />
    );
  } else {
    return (
      <div className="mdl-layout mdl-js-layout mdl-layout--fixed-header">
        <Loader loading={loading} theme={theme} />
        {warn ? (
          <div className="warn-con">
            <div className="warn-msg">
              You got a warning from Admin!
            <button className='mdl-button' onClick={() => setWarn(false)}>ok</button>
            </div>
          </div>
        ) : (
          ""
        )}
        <Please
          reRequest={reRequest}
          pause={pause}
          stop={false}
          never={false}
        />
        <themeContext.Provider value={{ theme, setTheme }}>
          <requestContext.Provider value={{ setRequest, setPost }}>
            <routeContext.Provider value={{ route, changeRoute }}>
              <layoutContext.Provider value={{ layout, changeLayout }}>
                <loaderContext.Provider value={{ setLoading }}>
                  <searchContext.Provider
                    value={{ searchResponse, setSearchResponse }}
                  >
                    <tabContext.Provider value={{ tab, setTab }}>
                      <Header
                        setCart={setCart}
                        dismiss={dismiss}
                        changeDismiss={changeHeaderDismiss}
                      />

                      <Drawer
                        notifyCount={notifyCount}
                        changeDismiss={changeDismiss}
                        webView={webView}
                      />
                      <Body
                        cart={cart}
                        setCart={setCart}
                        changeDismiss={changeDismiss}
                        dismiss={dismiss}
                        headerDismiss={headerDismiss}
                        checkKey={{ key, getKey }}
                      />
                    </tabContext.Provider>
                  </searchContext.Provider>
                </loaderContext.Provider>
              </layoutContext.Provider>
            </routeContext.Provider>
          </requestContext.Provider>
        </themeContext.Provider>
      </div>
    );
  }
  // return <h1>YAy</h1>;
}

export default () => <Store AppLoad={App} />;
