import React, { useContext } from "react";
import themeContext from "../../../../Context/themeContext";

function Clear({ saveCartList, changeToast, returnRemoved, setTool }) {
  const {theme} = useContext(themeContext);

  return (
    <div style={{ textAlign: "center", fontSize: 1.25 + "rem" }}>
      <div className="select tool-item" style={{ fontSize: 1 + "rem" }}>
        Are You Sure?
      </div>
      <div className="remove tool-item">
        <button
          className="mdl-button mdl-js-button mdl-js-ripple-effect"
          onClick={() => {
            saveCartList([]);
            changeToast("Cart Was Cleared", "warnning", {
              title: "UNDO",
              func: returnRemoved
            });
          }}
          style={theme.secBtn}
        >
          <i className="material-icons">check</i> Yes
        </button>
      </div>
      <div className="clear tool-item">
        <button
          className="mdl-button mdl-js-button mdl-js-ripple-effect"
          onClick={() => setTool("normal")}
          style={theme.secBtn}
        >
          <i className="material-icons">close</i> No
        </button>
      </div>
    </div>
  );
}
export default Clear;
