import React, { useContext } from 'react';
import themeContext from '../../../../Context/themeContext';

function Normal({setLastCart,setTool,cartList}) {
    const {theme} = useContext(themeContext);

    return (
        <div>
            <div className="select tool-item">
                <button
                    className="mdl-button mdl-js-button mdl-js-ripple-effect"
                    onClick={() => {
                        setTool("select");
                        setLastCart(cartList);
                    }}
                    style={theme.secBtn}
                >
                    <i className="material-icons">playlist_add_check</i> Select
          </button>
            </div>
            <div className="remove tool-item">
                <button
                    className="mdl-button mdl-js-button mdl-js-ripple-effect"
                    onClick={() => {
                        setTool("remove");
                        setLastCart(cartList);
                    }}
                    style={theme.secBtn}

                >
                    <i className="material-icons">remove_circle_outline</i> Remove
          </button>
            </div>
            <div className="clear tool-item">
                <button
                    className="mdl-button mdl-js-button mdl-js-ripple-effect"
                    onClick={() => {
                        setTool("clear");
                        setLastCart(cartList);
                    }}
                    style={theme.secBtn}

                >
                    <i className="material-icons">clear_all</i> Clear
          </button>
            </div>
        </div>
    )
}
export default Normal;