import React from 'react';

import './Toast.css';

function Toast({toast,setToast}){
    return (
        <div
            className={`toast ${toast.state ? "show-toast" : ""} ${toast.type}`}
        >
            <div className={`message ${toast.func ? "with-action" : ""}`}>
                {toast.text}
            </div>
            {toast.func ? (
                <button
                    className="action mdl-button mdl-js-button mdl-js-ripple-effect"
                    onClick={() => {
                        toast.func.func();
                        setToast({
                            state: false,
                            type: toast.type,
                            text: toast.text,
                            func: { title: toast.func.title, func: () => { } }
                        });
                    }}
                >
                    {toast.func.title}
                </button>
            ) : (
                    ``
                )}
        </div>
    )
}
export default Toast;
