import React, { useState, useEffect, useContext } from "react";
import "./Categories.css";
//component
import Selection from "./Selection/Selection";
import requestContext from "../../../Context/requestContext";
import CardsRender from "../CardsComonent/CardsRender";
import themeContext from "../../../Context/themeContext";
import List from "./List/List";
//
function Categories({ dismiss, headerDismiss, filter, setFilter, more, goTop }) {
  const [type, setType] = useState(false);
  const [data, setData] = useState([]);
  const [value, setValue] = useState(false);
  const [items, setItems] = useState([]);
  const [renderedItems, setRenderedItems] = useState([]);
  const [selected, setSelected] = useState("");
  //
  const { setRequest } = useContext(requestContext);
  const { theme } = useContext(themeContext);
  let mount = true;

  useEffect(() => { if (mount){
    if (mount) {
      const len = renderedItems.length;
      setRenderedItems(renderedItems.concat(items.slice(len, len + 18)))
    }
     
    // eslint-disable-next-line
        return () => (mount = false);
  }}, [mount,more]);
  useEffect(() => { if (mount){
    if (type === "Genre") {
      setRequest("getCategories", false).then((e) => {
        if (e) {
          if (mount) {
            setData(e);
          }
        }
      });
    } else if (type === "Time") {
      setRequest("getMovieReleased", false).then((e) => {
        if (e) {
          if (mount) {
            setData(e);
          }
        }
      });
    } else if (type) {
      setRequest('otherCatagories', false, type).then(e => {
        if (e) {
          if (mount) {
            setData(e);
          }
        }
      })
    }
     
    // eslint-disable-next-line
        return () => (mount = false);
  }}, [mount,type]);
  useEffect(() => { if (mount){
    if (mount) {
      if (selected !== "") {
        getCard(selected);
      }
    }
     
    // eslint-disable-next-line
        return () => (mount = false);
  }}, [mount,filter]);
  const getCard = (name, otherName = false) => {
    setSelected(name);
    if (type === "Genre") {
      setRequest("getCategoryValue", false, { name, filter }).then((e) => {
        if (e) {
          setValue(name);
          setItems(e);
          setRenderedItems(e.slice(0, 18))
          goTop();
        }
      });
    } else if (type === "Time") {
      setRequest("getMovieReleasedValue", false, { name, filter }).then((e) => {
        if (e) {
          setValue(name);
          setItems(e);
          setRenderedItems(e.slice(0, 18))
          goTop();
        }
      });
    } else {
      setRequest('movieBunch', false, { name, filter }).then(e => {
        if (e) {
          if (otherName) {
            setValue(otherName);
          }
          setItems(e);
          setRenderedItems(e.slice(0, 18))
          goTop();
        }
      })
    }
  };
  const navigator = () => {
    setItems([]);
    if (value) {
      setValue(false);
      // setData([]);
    } else {
      if (type) {
        setData([]);
        setType(false);
      }
    }
  };

  let separate = (function () {
    let counter = 0;
    return function (value) {
      let year = Number(value.slice(0, 4));
      if (counter === 0) {
        counter = year;
      } else {
        if (year !== counter) {
          counter = year;
          return true;
        }
      }
      return false;
    };
  })();

  return (
    <div className="cat">
      <div className="navigator" onClick={navigator} style={theme.itemLite}>
        Categories {type ? " > " + type.slice(0, 1).toUpperCase() + type.substring(1) : ""}{" "}
        {type && value ? " > " + value : ""}
      </div>
      {!type ? (
        <div className="content">
          <Selection setType={setType} />
        </div>
      ) : !value ? (
        <List type={type} data={data} separate={separate} theme={theme} getCard={getCard} more={more} init={!(!!value)} />
      ) : (
            <div className="lower">
              <CardsRender
                items={renderedItems}
                dismiss={dismiss}
                headerDismiss={headerDismiss}
                filter={filter}
                setFilter={setFilter}
              />
            </div>
          )}
    </div>
  );
}
export default Categories;
