import React from 'react'

function Box({ data, id }) {



  // console.log({data});
  const rating = data.rating;
  // console.log({rating});
  let starUp = Math.round(rating / 2);
  let starDown = Math.floor(rating / 2);
  let starNode = [];
  let starDim = [];
  let starHalf = true;
  for (let starDI = starUp + 1; starDI <= 5; starDI++) {
    starDim.push(starDI);
  }
  for (let starI = 0; starI < starUp - 1; starI++) {
    starNode.push(starI);
  }
  if (starDown === starUp) {
    starHalf = false;
  }

  return (
    <div className="box">
      <div className="poster">
        <img src={`http://${window.endPoint}${data.img}`} alt="poster" />
      </div>
      <div className="info">
        <div className="title">
          <span className="rank">{id}.</span>
          <span className="name">{data.name}</span>
        </div>
        {data.content.map((i, key) => {
          const value = i.split(":");
          return (
            <div className={`${value[0]} bold-des`} key={key}>
              <span>{value[0]}: </span>
              {value[1]}
            </div>
          )
        })}
        <div className="rating">
          <span>Current Rating: </span>{rating}
            <span className="rate">
            <div className="star">
              {starNode.map(star => (
                <i className="material-icons light" key={star}>
                  star
                </i>
              ))}
              <i className="material-icons light">
                {starHalf ? `star_half` : `star`}
              </i>
              {starDim.map(i => (
                <i className="material-icons" key={i}>
                  star
                </i>
              ))}
            </div>
          </span>
        </div>
      </div>
    </div>
  );
}
export default Box;