import React, { useContext } from "react";
import { layoutContext } from "../../../Context/layoutContext";
import { routeContext } from "../../../Context/routeContext";
import themeContext from "../../../Context/themeContext";
import Card from "./Card/Card";

import FakeCard from "./FakeCard/FakeCard";

function CardsRender({
  items,
  dismiss,
  headerDismiss,
  highLightFor,
  filter,
  setFilter,
  fetching,
}) {
  //
  const { route } = useContext(routeContext);
  const { theme } = useContext(themeContext);
  const { layout } = useContext(layoutContext);
  //

  let fake = [];
  let length = 18;
  if (layout === "column") {
    length = 6;
  } else if (layout === "small-list") {
    length = 8;
  }
  for (let i = 0; i < length; i++) {
    fake.push(<FakeCard key={i} />);
  }
  return (
    <div>
      {route === "movie" && (
        <div className="filter-con">
          <div className="filter" style={theme.itemCon}>
            <div
              className={`filter-item a ${!filter && "active"}`}
              onClick={() => setFilter(false)}
              style={!filter ? theme.itemSelect : theme.itemSoftDarker}
            >
              <i className="material-icons">sort</i>
              <span>All</span>
            </div>
            <div
              className={`filter-item ${filter === "movie" && "active"}`}
              onClick={() => setFilter("movie")}
              style={
                filter === "movie" ? theme.itemSelect : theme.itemSoftDarker
              }
            >
              <i className="material-icons">movie</i>
              <span>Movies</span>
            </div>
            <div
              className={`filter-item tv ${filter === "series" && "active"}`}
              onClick={() => setFilter("series")}
              style={
                filter === "series" ? theme.itemSelect : theme.itemSoftDarker
              }
            >
              <i className="material-icons">tv</i>
              <span>TV Series</span>
            </div>
          </div>
        </div>
      )}
      {items[0]
        ? items.map((item, i) => (
            <Card
              item={item}
              order={i + 1}
              key={i}
              dismiss={dismiss}
              headerDismiss={headerDismiss}
              highLightFor={highLightFor}
            />
          ))
        : route === "movie" && fake.map((i) => i)}
      {fetching && <div className="getting-more">Getting more...</div>}
    </div>
  );
}
export default CardsRender;
