import React, { useContext } from "react";
import themeContext from "../../../Context/themeContext";
//style
import "./More.css";
//
function More({ changeLayout }) {
  const {theme} = useContext(themeContext);
  //
  return (
    <div className="more">
      <button
        id="demo-menu-lower-right"
        className="mdl-button mdl-js-button mdl-button--icon"
      >
        <i className="material-icons">more_vert</i>
      </button>

      <ul
        className="mdl-menu mdl-menu--bottom-right mdl-js-menu mdl-js-ripple-effect face"
        htmlFor="demo-menu-lower-right"
        style={theme.item}
      >
        <li
          className="mdl-menu__item"
          onClick={() => {
            changeLayout("column");
          }}
          style={theme.secBtn}
        >
          <i className="material-icons" style={theme.secBtn}>view_column</i>
          Large
        </li>
        <li
          className="mdl-menu__item"
          onClick={() => {
            changeLayout("grid");
          }}
          style={theme.secBtn}
        >
          <i className="material-icons" style={theme.secBtn}>view_module</i>
          Small
        </li>
        <li
          className="mdl-menu__item"
          onClick={() => {
            changeLayout("small-list");
          }}
          style={theme.secBtn}
        >
          <i className="material-icons" style={theme.secBtn}>view_headline</i>
          List
        </li>
      </ul>
    </div>
  );
}

export default More;
