import React, { useEffect } from "react";
//style
import "./Logo.css";
//
function Logo() {
  const storeId = JSON.parse(localStorage.store).id;
  useEffect(() => {
    const href = `/storage/storeImg/${storeId}l.png`;
    document.getElementById('app-icon').setAttribute('href',href);
  }, [storeId]);
  return (
    <div className="material-image" width="240" height="240">
      <img src={`http://${window.endPoint}/storage/storeImg/${storeId}s.png`} alt="" />
    </div>
  );
}

export default Logo;
