import React, { useState, useContext } from "react";
//style
import "./Cart.css";
//componet
import CartList from "./CartList/CartList";
import Clear from "./Tools/Clear";
import Select from "./Tools/Select";
import Remove from "./Tools/Remove";
import Normal from "./Tools/Normal";
//functions
import {  add, prepareArray } from "./Func";
//context
import cartContext from "./../../../Context/cartContext";
import requestContext from "./../../../Context/requestContext";
import { routeContext } from "./../../../Context/routeContext";
import { formatSize } from "../../../Function";
import themeContext from "../../../Context/themeContext";
//
function Cart({ removeCart, visibility, dismiss, changeToast,lock,setLock }) {
  const [tool, setTool] = useState("normal");
  const [selected, setSelected] = useState([]);
  const [removed, setRemoved] = useState(0);
  const [lastCart, setLastCart] = useState([]);
  const [settings, setSettings] = useState(false);
 
  //
  const { cartList, saveCartList, removeCartList,settingsData,setSettingsData } = useContext(cartContext);
  const { setPost } = useContext(requestContext);
  const { changeRoute } = useContext(routeContext);
  const {theme} = useContext(themeContext);

  //
  const total = add(cartList);
  const addSelected = (item) => {
    setSelected([...selected, item]);
  };
  const removeSelected = (item) => {
    setSelected(prepareArray(selected, item));
  };
  const changeRemoved = () => {
    setRemoved(removed + 1);
    if (cartList.length === 1) {
      changeToast(
        `${removed + 1} ${removed > 1 ? "Items" : "Item"} Removed`,
        "warnning",
        { title: "UNDO", func: returnRemoved }
      );
    }
  };
  const returnRemoved = () => {
    saveCartList(lastCart);
    setTool("normal");
    setRemoved(0);
    setSelected([]);
  };
  const setRemovedDelay = () => {
    setRemoved(0);
  };
  const dismissAll = () => {
    if (tool === "remove" && cartList.length !== 0 && removed !== 0) {
      changeToast(
        `${removed} ${removed > 1 ? "Items" : "Item"} Were Removed`,
        "warnning",
        { title: "UNDO", func: returnRemoved }
      );
    }
    setTool("normal");
    setSelected([]);
    setTimeout(setRemovedDelay, 3000);
  };
  const selectedRemoval = () => {
    if (selected.length) {
      changeToast(
        `${selected.length} ${
          selected.length > 1 ? "Items" : "Item"
        } Were Removed`,
        "warnning",
        { title: "UNDO", func: returnRemoved }
      );
    } else {
      changeToast("First select items to remove", "error");
    }
    removeCartList(selected, false);
    setSelected([]);
    setTool("normal");
  };
  const handleSettings = () => {
    setSettings(false);
    /**
    |--------------------------------------------------
    | settings logic
    |--------------------------------------------------
    */
  };
  if (dismiss && tool !== "normal") {
    dismissAll();
  }

  let hide = cartList.length === 0 ? "hide" : "";
  let show = cartList.length === 0 ? "" : "hide";
  //
  const download = (list = false) => {
    setPost("authDownload", {
      list: cartList,
      settngs: settingsData,
    }).then((e) => {
      if (e) {
        changeRoute("download");
      }
    });
  };
  let toolNode;
  if (tool === "normal") {
    toolNode = (
      <Normal setTool={setTool} setLastCart={setLastCart} cartList={cartList} />
    );
  } else if (tool === "select") {
    toolNode = (
      <Select
        setTool={setTool}
        selectedRemoval={selectedRemoval}
        selected={selected}
        download={download}
      />
    );
  } else if (tool === "remove") {
    toolNode = <Remove removed={removed} dismissAll={dismissAll} />;
  } else if (tool === "clear") {
    toolNode = (
      <Clear
        saveCartList={saveCartList}
        changeToast={changeToast}
        returnRemoved={returnRemoved}
        setTool={setTool}
      />
    );
  }

  return (
    <div className={`cart-con ${visibility}`}>
      <div className={`cart ${lock && 'lock'}`} id="cart" style={theme.bg}>
        <div className={`total ${settings ? "s" : ""}`} style={theme.item}>
          <div className={settings ? "hide" : ""}>
            <h6 style={theme.borderBottom}>Total</h6>
            <button className="mdl-button mdl-js-button mdl-button--icon mdl-button--colored lock" onClick={()=>setLock(!lock)}>
              {lock?
                <i className="material-icons">lock</i>
              :<i className="material-icons">lock_open</i>}
            </button>
            <button
              className="mdl-button mdl-js-button mdl-js-ripple-effect mdl-button--accent btn-download"
              disabled={!(cartList.length > 0)}
              onClick={() => (cartList.length > 0 ? download() : "")}
            >
              <i className="material-icons">file_download</i> Download
            </button>
            <div className={hide}>
              <div className="total-child price">
                <span>{total.price}</span>
                ETB
              </div>
              <div className="total-child size">
                <span>{formatSize(total.size, true)}</span>
                {formatSize(total.size, false, true)}
              </div>
            </div>
            <div className={`empty ${show}`}>
              <div className="head">Cart Is Empty</div>
              <div className="body">
                All The Movies You Would Like To Buy Will Be Listed Here. Just
                Click on The Movies and Press "Add To Cart".
              </div>
            </div>

            <div className="settings">
              <button className="btn" onClick={() => setSettings(true)}>
                <i className="material-icons">settings</i>
                Settings
              </button>
            </div>
          </div>
          <div className={`settings-tab ${settings ? "" : "hide"}`}>
            <h6>Settings</h6>
            <div className="subtitles">
              <div className="label">Subtitles</div>
              <div className="input-check">
                <label
                  className="mdl-switch mdl-js-switch mdl-js-ripple-effect"
                  htmlFor="list-switch-1"
                >
                  <input
                    type="checkbox"
                    id="list-switch-1"
                    className="mdl-switch__input"
                    checked={settingsData.subtitles.state}
                    onChange={() => {
                      setSettingsData({
                        subtitles: {
                          state: !settingsData.subtitles.state,
                          languge: settingsData.subtitles.languge,
                        },
                        quality: settingsData.quality,
                      });
                    }}
                  />
                </label>
              </div>
            </div>
            <div className="select">
              <div className="sec two">
                <label htmlFor="languge" className="select-label">
                  Languge:
                </label>
                <select
                  id="languge"
                  onChange={(e) => {
                    setSettingsData({
                      subtitles: {
                        state: settingsData.subtitles.state,
                        languge: e.target.value,
                      },
                      quality: settingsData.quality,
                    });
                  }}
                  value={settingsData.subtitles.languge}
                >
                  <option value="english">English</option>
                  <option value="amharic">Amharic</option>
                  <option value="oromiffa">Oromiffa</option>
                  <option value="tegrigna">Tegrigna</option>
                </select>
              </div>
              <div className="sec">
                <label htmlFor="languge" className="select-label">
                  Quality:
                </label>
                <select
                  id="languge"
                  onChange={(e) => {
                    setSettingsData({
                      subtitles: {
                        state: settingsData.subtitles.state,
                        languge: settingsData.subtitles.languge,
                      },
                      quality: e.target.value,
                    });
                  }}
                  value={settingsData.quality}
                >
                  <option value="360">360p</option>
                  <option value="720">720p</option>
                  <option value="1080">1080p</option>
                </select>
              </div>
            </div>
            <div className="cau">
              This configuration will be applied to all items and gets saved.
            </div>
            <div className="actions">
              <button
                className="mdl-button mdl-js-button mdl-button--raised mdl-button--accent"
                onClick={() => handleSettings(false)}
              >
                Set
              </button>
            </div>
          </div>
        </div>
        <div className={`tool ${hide}`} style={theme.secBtn}>{toolNode}</div>
        <div className="cart-list">
          {cartList.map((item) => (
            <CartList
              item={item}
              key={item.id}
              tool={tool}
              removeCartList={removeCartList}
              addSelected={addSelected}
              removeSelected={removeSelected}
              changeRemoved={changeRemoved}
              dismiss={dismiss}
            />
          ))}
        </div>
      </div>
      <div
        className="cart-bg"
        onClick={() => {
          removeCart();
        }}
      />
    </div>
  );
}

export default Cart;
