import React, { useContext, useState } from "react";
import cartContext from "../../../Context/cartContext";
import requestContext from "../../../Context/requestContext";

import "./Fab.css";

let scroll = 0;
let lastPosition = 0;
function Fab({ position, goTop, changeRoute, cart, lock }) {
  const [glow, setGlow] = useState("");
  const [fabVisibility, setFabVisibility] = useState(true);
  const [getBack, setGetBack] = useState(false);
  const { setPost } = useContext(requestContext);
  const { cartList, settingsData } = useContext(cartContext);
  const removeGlow = () => {
    setGlow("");
  };
  const changeCart = () => {
    if (cart) {
      if(cartList.length){
        setPost("authDownload", {
          list: cartList,
          settngs: settingsData,
        }).then((e) => {
          if (e) {
            changeRoute("download");
          }
        });
      }
    } else {
      changeRoute("cart");
    }
  };
  function handleScroll() {
    scroll = position();
    if (scroll !== lastPosition) {
      if (scroll < lastPosition) {
        setFabVisibility(true);
      } else {
        setFabVisibility(false);
      }
      lastPosition = scroll;
    }
    if (scroll > 700) {
      if (!getBack) setGetBack(true);
    } else {
      if (getBack) setGetBack(false);
    }
  }
  setInterval(handleScroll, 500);

  return (
    <div>
      <div
        className={`btn-con btn-corner ${
          fabVisibility ? "btn-rise" : "btn-drop"
        }`}
      >
        <div className={`glow ${glow}`} />
        <button
          className={`mdl-button mdl-js-button mdl-button--raised mdl-button--fab mdl-button--colored mdl-js-ripple-effect btn-itself ${cart && 'download-color'}`}
          onClick={(e) => {
            setTimeout(changeCart, 280);
            setGlow("show");
            setTimeout(removeGlow, 400);
          }}
        >
          <i className="material-icons">
            {cart ? `file_download` : `shopping_cart`}
          </i>
        </button>
      </div>
      {!lock && (
        <div
          className={`getback-con ${
            getBack ? "get-back-rise" : "get-back-drop"
          }`}
          onClick={() => {
            goTop();
          }}
        >
          <div className="curt">
            <i className="material-icons">keyboard_arrow_up</i>
          </div>
          <button className="mdl-button mdl-js-button mdl-button--raised mdl-button--fab mdl-button--colored mdl-js-ripple-effect btn-itself">
            <i className="material-icons">shopping_cart</i>
          </button>
        </div>
      )}
    </div>
  );
}

export default Fab;
