import React, { useEffect, useContext, useState } from "react";

import "./SearchResult.css";
//context
import searchContext from "./../../../Context/searchContext";
//compo
import CardsRender from "./../CardsComonent/CardsRender";
import requestContext from "../../../Context/requestContext";
//
function SearchResult() {
  const { searchResponse } = useContext(searchContext);
  const { setRequest } = useContext(requestContext);
  //
  const [response, setResponse] = useState(false);
  const [resHeader, setResHeader] = useState("");
  const [cardArray, setCardArray] = useState([]);
  const [err, setErr] = useState(false);
  const [addedWishlist, setAddedWishList] = useState(false);
  const res = searchResponse;
  let mount = true;

  useEffect(() => {
    if (mount) {
      if (res) {
        setResponse(true);
        setAddedWishList(false);
        setResHeader(
          res.found
            ? `${res.amount} Result${res.amount > 1 ? "s" : ""} for "${res.for}`
            : `No results were found for "${res.for}`
        );
        setCardArray(res.data);
        if (res.found) {
          setErr(false);
        } else {
          setErr(true);
        }
      }
    }

    // eslint-disable-next-line
    return () => mount = false;
  }, [mount, res]);
  function addToWishlist() {
    if(!addedWishlist){
      setRequest("addWishlist", false, res.for).then((e) => {
        if (e) {
          setAddedWishList(true);
        }
      });
    }
  }
  return (
    <div className="search-result">
      {/*  */}
      <div
        className={`empty-search ${response && err ? "" : "hide"} no-result`}
      >
        <i className="material-icons i">sentiment_very_dissatisfied</i>
        <div>
          <span className="txt">{resHeader}</span>
          <span className="cot">"</span>
        </div>
        <div className="low">
          Check your spelling or Try fewer letters maybe.
        </div>

        <button
          className={`mdl-button mdl-js-button mdl-js-ripple-effect ${addedWishlist ? "add-done" : "mdl-button--raised"
            }`}
          onClick={addToWishlist}
        >
          {!addedWishlist ? (
            <span>
              <i className="material-icons">add</i>
              Add to Wishlist
            </span>
          ) : (
              <span>
                <i className="material-icons">done</i>
              Done
              </span>
            )}
        </button>
      </div>
      {/*  */}
      <div className={`empty-search ${response ? "hide" : ""}`}>
        <i className="material-icons i">search</i>
        <div>Search is on the way!</div>
        <div className="low">Start typing to see suggestions</div>
      </div>
      {/*  */}
      <div className={`result-header ${response && !err ? "" : "hide"}`}>
        <span className="txt">{resHeader}</span>
        <span className="cot">"</span>
      </div>
      {/*  */}
      <CardsRender items={cardArray} highLightFor={res ? res.for : false} />
    </div>
  );
}

export default SearchResult;
