import React, { useContext, useEffect, useState } from "react";
import requestContext from "../../../Context/requestContext";
import themeContext from "../../../Context/themeContext";
import "./Notify.css";
//
function Notify() {
  const { setRequest } = useContext(requestContext);
  const { theme } = useContext(themeContext);
  const [n, setN] = useState([]);
  const [remain, setRemain] = useState(false);
  const img = (automated) => {
    if (automated) {
      return <i className="material-icons" style={{fontSize:'32px'}}>update</i>
    }else{
      return (
        <img
          src={`http://${window.endPoint}/storage/storeImg/${JSON.parse(localStorage.store).id}l.png`}
          alt="app"
        />
      )
    }
  };
  let mount = true;

  useEffect(() => {
    if (mount) {
      setRequest("notifications", false, { fn: localStorage.fn?JSON.parse(localStorage.fn):false }).then((e) => {
        if (e) {
          if (e[0]) {
            if (mount) {
              setN(e);
              if (localStorage.notifyCount) {
                setRemain(e.filter(data => !data.automated).length - localStorage.notifyCount);
              }
              localStorage.notifyCount = e.filter(data => !data.automated).length;
            }
          }
        }
      });

      // eslint-disable-next-line
      return () => (mount = false);
    }
  }, [mount]);
  return (
    <div className="notify">
      {n[0] ? (
        n.map((item, key) => (
          <React.Fragment key={key}>
            <div className="item" style={theme.item}>
              <div className="head" style={theme.itemTransparent}>
                <div className="img">{img(item.automated)}</div>
                <div className="text">
                  <div className="name">{item.admin} {item.automated?'[Update]':''}</div>
                  <div className="time">{item.time}</div>
                </div>
              </div>
              <div className="body">{item.value}</div>
              <div className="additional">{item.automated ? item.additional : ""}</div>
            </div>
            {remain === key + 1 && <div className="hr">Older</div>}
          </React.Fragment>
        ))
      ) : (
        <div className="empty">
          <i className="material-icons">notifications_none</i>
          No notifications for now
        </div>
      )}
    </div>
  );
}
export default Notify;
