import React, { useContext } from "react";
import themeContext from "../../Context/themeContext";
//style
import "./Drawer.css";
//component
import Logo from "./Logo/Logo";
import Option from "./Option/Option";
//
function SideBar({ notifyCount,changeDismiss,webView}) {
  const {theme} = useContext(themeContext);
  //
  return (
    <div className="mdl-layout__drawer" onClick={() => changeDismiss()} style={theme.bg}>
      <Logo />
      <Option notifyCount={notifyCount} webView={webView}/>
    </div>
  );
}

export default SideBar;
