function serverMan(whatYouWant, prams = '', key = '', tab = 1) {
  let cont = encodeURIComponent(prams);
  if (cont === '%5Bobject%20Object%5D') {
    cont = prams;
  }
  let storeId = '';
  if (localStorage.store) {
    storeId = `&store=${JSON.parse(localStorage.store).id}`;
  }else{
    
  }
  const result = fetch(
    `http://${window.endPoint}/api/${whatYouWant}?prams=${JSON.stringify({
      value: cont
    })}&key=${key}&tab=${tab}${storeId}`,
    {
      mode: "cors"
    }
  )
    .then(e => e.json())
    .catch(err => console.log("ERROR:", err));
  return result;
}

export default serverMan;
