import React, { useContext } from 'react'
import './Selection.css';
import themeContext from '../../../../Context/themeContext';
//
function Selection({ setType }) {
  const { theme } = useContext(themeContext);
  return (
    <div className="selection">

      <div className="box genre" onClick={() => setType('Genre')} style={theme.item}>
        <div className="icon">
          <i className="material-icons">movie</i>
        </div>
        <div className="text">
          <div>Genre</div>
          <span>of Movies</span>
        </div>
      </div>
      <div className="box time" onClick={() => setType('Time')} style={theme.item}>
        <div className="icon">
          <i className="material-icons">access_time</i>
        </div>
        <div className="text">
          <div>Time</div>
          <span>of Release</span>
        </div>
      </div>
      <div className="box sea s" onClick={() => setType('directors')} style={theme.item}>
        <div className="icon">
          <i className="material-icons">directions</i>
        </div>
        <div className="text">
          <div>Directors</div>
          <span>of Movies</span>
        </div>
      </div>


      <div className="box actors" onClick={() => setType('actors')} style={theme.item}>
        <div className="icon">
          <i className="material-icons">person</i>
        </div>
        <div className="text">
          <div>Actors</div>
          <span>participated</span>
        </div>
      </div>
      <div className="box production" onClick={() => setType('production')} style={theme.item}>
        <div className="icon">
          <i className="material-icons">videocam</i>
        </div>
        <div className="text">
          <div>Production</div>
          <span>done by</span>
        </div>
      </div>
      <div className="box language sea" onClick={() => setType('language')} style={theme.item}>
        <div className="icon">
          <i className="material-icons">language</i>
        </div>
        <div className="text">
          <div>Language</div>
          <span>used</span>
        </div>
      </div>
    </div>
  );
}
export default Selection;