/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable array-callback-return */
import React, { useContext, useEffect, useState } from "react";
import cartContext from "../../../../../Context/cartContext";
import movieContext from "../../../../../Context/movieContext";
import requestContext from "../../../../../Context/requestContext";
import { routeContext } from "../../../../../Context/routeContext";
import themeContext from "../../../../../Context/themeContext";
import { formatSize } from "../../../../../Function";

import "./CardSeriesDetail.css";

function CardSeriesDetail({
  item,
  setCardState,
  setShowState,
  pullUp,
  disableMoreDetails,
}) {
  const seasons = item.seasons.sort((a, b) => Number(a.value) - Number(b.value));
  const [all, setAll] = useState({ size: 0, price: 0 });
  const [checked, setChecked] = useState(false);
  const [self, setSelf] = useState(false);
  const [selected, setSelected] = useState([]);
  const [amount, setAmount] = useState({ se: 0, ep: 0 });
  const [addCart, setAddCart] = useState(true);
  const [notificationActive, setNotificationActive] = useState(false);

  const { changeRoute } = useContext(routeContext);
  const { setRequest } = useContext(requestContext);
  const { setMovie } = useContext(movieContext);
  const { cartList, changeCartList } = useContext(cartContext);
  const { theme } = useContext(themeContext);

  //
  function close() {
    if (setShowState) {
      setShowState(false);
    } else {
      setCardState(false);
    }
  }
  let mount = true;

  useEffect(() => {
    if (mount) {
      if (mount) {
        let ep = 0;
        seasons.map((i) => {
          ep += i.episodes.length;
        });
        setAmount({ se: seasons.length, ep });
        cartList.map((cart) => {
          if (cart.id === item.movie.id) {
            setSelected(cart.ep);
          }
        });
        cartList.map((i) => {
          if (i.id === item.movie.id) {
            if (addCart) {
              setAddCart(false);
            }
          }
        });
      }
      // eslint-disable-next-line
      return () => (mount = false);
    }
  }, [mount]);
  useEffect(() => {
    if (mount) {
      if (mount) {
        if (!checked && self) {
          setSelected([]);
        }
        if (checked && self) {
          let newArray = [];
          seasons.map((s) => {
            s.episodes.map((e) => {
              newArray.push(e);
            });
          });
          setSelected(newArray);
        }
      }
      // eslint-disable-next-line
      return () => (mount = false);
    }
  }, [mount, checked, self]);
  useEffect(() => {
    if (mount) {
      if (mount) {
        if (selected.length > amount.ep) {
          setSelected([]);
          changeSelf(false);
        }
      }
      // eslint-disable-next-line
      return () => (mount = false);
    }
  }, [mount, selected.length]);
  function changeSelf(value) {
    setSelf(true);
    setChecked(value);
    setTimeout(() => setSelf(false), 50);
  }

  function createSelected(info, check) {
    let newArray = [];

    if (check) {
      if (self) {
        newArray = selected;
        newArray.push(info);
        setSelected(newArray);
      } else {
        setSelected([...selected, info]);
      }
    } else {
      if (!self) {
        selected.map((i) => {
          if (i.id !== info.id) {
            newArray.push(i);
          }
        });
        setSelected(newArray);
      }
    }
  }

  function createSeason(season, check) {
    let every = selected;
    let newArray = [];
    if (!self) {
      if (check) {
        //create
        season.episodes.map((i) => {
          let theArray = [];
          every.map((k) => {
            if (i.id !== k.id) {
              theArray.push(k);
            }
          });
          every = theArray;
        });
        newArray = every;
        season.episodes.map((i) => {
          newArray.push(i);
        });
        setSelected([...newArray]);
      } else {
        season.episodes.map((i) => {
          let theArray = [];
          every.map((k) => {
            if (i.id !== k.id) {
              theArray.push(k);
            }
          });
          every = theArray;
        });
        setSelected([...every]);
      }
    }
  }
  function getMetric() {
    let s = 0;
    let p = 0;

    selected.map((i) => {
      s += Number(i.size);
      p += Number(i.price);
    });

    return { size: s, price: p };
  }
  function addToCart() {
    if (selected.length > 0) {
      let name = item.movie.title ? item.movie.title : item.movie.name;
      changeCartList(
        {
          id: item.movie.id,
          name: name,
          price: getMetric().price,
          size: getMetric().size,
          ep: selected,
        },
        "",
        true
      );
      close();
      setAddCart(false);
    }
  }
  function removeRemove() {
    if (!addCart) {
      setAddCart(true);
    }
  }
  const changeNotification = () => {
    if (!notificationActive) {
      const stuff = { m: item.movie.id, t: new Date() };
      const prev = JSON.parse(localStorage.fn || "[]");
      const newer = prev.filter(obj => obj.m !== item.movie.id)
      newer.push(stuff)
      localStorage.fn = JSON.stringify(newer)
    }else{
      localStorage.fn = JSON.stringify(JSON.parse(localStorage.fn).filter(obj=>obj.m !== item.movie.id));
    }
    setNotificationActive(!notificationActive)
  }
  //
  return (
    <div className="card-s-d">
      <div className="dark-bg" onClick={close}></div>
      <div className={`content ${pullUp && "pull-up"}`} style={theme.bg}>
        <div className="close">
          <button
            className="mdl-button mdl-js-button mdl-js-ripple-effect"
            onClick={close}
          >
            <i className="material-icons">close</i>

            <span>close</span>
          </button>
        </div>
        <div className="main" style={theme.item}>
          <div className="left">
            <div className="title">
              {item.movie.title ? item.movie.title : item.movie.name}
            </div>
            <span>
              {amount.se} Seasons, {amount.ep} Episodes
            </span>
          </div>
          <div className="right">
            <div className="head">Selected</div>
            <span className="info">
              <span className="p">
                {getMetric().price}
                <span>ETB</span>
              </span>
              <span className="s">
                {formatSize(getMetric().size, true)}
                <span>{formatSize(getMetric().size, false, true)}</span>
              </span>
            </span>
          </div>
        </div>

        <div className="top">
          {!disableMoreDetails && (
            <button
              className={`see-more mdl-button mdl-js-button mdl-js-ripple-effect`}
              onClick={() => {
                setRequest(
                  "movie",
                  {},
                  { id: item.movie.id }
                ).then((e) => {
                  if (e) {
                    setMovie(e);
                    close();
                    changeRoute("movie detail");
                  }
                });
              }}
              style={theme.secBtn}
            >
              <span>More Details</span>
            </button>
          )}
          <span className="add-btns">
            <button className="mdl-button mdl-js-button mdl-button--icon bell-btn" onClick={changeNotification}>
              <i className="material-icons">{notificationActive ? 'notifications_active' : 'notifications_none'}</i>
            </button>
            {addCart ? (
              <button
                className={`add-cart mdl-button mdl-js-button mdl-js-ripple-effect mdl-button--raised mdl-button--accent`}
                onClick={() => addToCart()}
                disabled={!(selected.length > 0)}
              >
                <i className="material-icons">add_shopping_cart</i>
                <span>Add To Cart</span>
              </button>
            ) : (
              <button
                className="red add-cart mdl-button mdl-js-button mdl-js-ripple-effect mdl-button--raised mdl-button--accent"
                onClick={() => {
                  changeCartList(item.movie.id, "remove");
                  setAddCart(true);
                }}
              >
                <i className="material-icons">clear</i>
                <span>Remove</span>
              </button>
            )}
          </span>
        </div>

        <div className="list">
          <div className="item" style={theme.item}>
            <label
              className="mdl-checkbox mdl-js-checkbox mdl-js-ripple-effect"
              htmlFor="checkbox-all"
            >
              <input
                type="checkbox"
                id="checkbox-all"
                className="mdl-checkbox__input"
                checked={checked}
                onChange={() => changeSelf(!checked)}
              />
              <span className="mdl-checkbox__label">All</span>
              <span className="prices">{all.price} ETB</span>
              <span className="sizes">{formatSize(all.size)}</span>
            </label>
          </div>
          {seasons.map((i, key) => (
            <Season
              season={i}
              setAll={setAll}
              allCheck={checked}
              setAllCheck={setChecked}
              allSelf={self}
              createSelected={createSelected}
              createSeason={createSeason}
              selected={selected}
              movieId={item.movie.id}
              removeRemove={removeRemove}
              key={key}
            />
          ))}
          {/* <Item id={0}/> */}
          <div className="gut"></div>
        </div>
      </div>
    </div>
  );
}

export default CardSeriesDetail;
function Season({
  season,
  setAll,
  allCheck,
  setAllCheck,
  allSelf,
  createSelected,
  createSeason,
  selected,
  movieId,
  removeRemove,
}) {
  const [checked, setChecked] = useState(true);
  const [self, setSelf] = useState(false);
  const { theme } = useContext(themeContext);

  //
  let mount = true;
  useEffect(() => {
    if (mount) {
      if (mount) {
        if (allSelf) {
          setSelf(true);
          setChecked(allCheck);
          setTimeout(() => setSelf(false), 50);
        }
      }
      // eslint-disable-next-line
      return () => (mount = false);
    }
  }, [mount, allCheck]);
  useEffect(() => {
    if (mount) {
      if (mount) {
        if (!self) {
          setAllCheck(false);
        }
        if (self) {
          createSeason(season, checked);
          removeRemove();
        }
      }
      // eslint-disable-next-line
      return () => (mount = false);
    }
  }, [mount, checked]);
  function change() {
    if (allCheck && checked) {
      setAllCheck(false);
    }
    setChecked(!checked);
  }
  function changeSelf() {
    setSelf(true);
    change();
    setTimeout(() => setSelf(false), 500);
  }
  function removeSeason() {
    if (checked) {
      setChecked(false);
    }
  }
  let size = 0;
  let price = 0;
  const theEpisodes = season.episodes;
  theEpisodes.sort(function (x, y) {
    return x.value - y.value;
  });
  const episodes = theEpisodes.map((episode, key) => {
    size += Number(episode.size);
    price += Number(episode.price);
    return (
      <Episode
        id={episode.value}
        info={episode}
        seasonCheck={checked}
        setSeasonCheck={setChecked}
        self={self}
        createSelected={createSelected}
        selected={selected}
        movieId={movieId}
        removeSeason={removeSeason}
        removeRemove={removeRemove}
        key={key}
      />
    );
  });
  useEffect(() => {
    if (mount) {
      if (mount) {
        setAll((prev) => {
          return { size: prev.size + size, price: prev.price + price };
        });
      }
      // eslint-disable-next-line
      return () => (mount = false);
    }
  }, [mount]);
  const strId = `checkbox-${season.value + Math.random()}`;

  return (
    <div className="sn">
      <div className="item" style={theme.item}>
        <label
          className="mdl-checkbox mdl-js-checkbox mdl-js-ripple-effect"
          htmlFor={strId}
        >
          <input
            type="checkbox"
            id={strId}
            className="mdl-checkbox__input"
            checked={checked}
            onChange={changeSelf}
          />{" "}
          <span className="title">
            S<span>eason</span> {season.value}
          </span>
          <span className="prices">{price} ETB</span>
          <span className="sizes">{formatSize(size)}</span>
        </label>
      </div>
      {episodes}
    </div>
  );
}
function Episode({
  id,
  info,
  seasonCheck,
  setSeasonCheck,
  self,
  createSelected,
  movieId,
  removeSeason,
  removeRemove,
}) {
  const [checked, setChecked] = useState(false);
  const [mySelf, setMySelf] = useState(true);
  const { cartList } = useContext(cartContext);
  const { theme } = useContext(themeContext);
  //
  let mount = true;
  useEffect(() => {
    if (mount) {
      if (mount) {
        if (!self && mySelf) {
          createSelected(info, checked);
          removeRemove();
        }
      }
      // eslint-disable-next-line
      return () => (mount = false);
    }
  }, [mount, checked]);
  function changeMySelf() {
    setMySelf(false);
    setChecked(true);
    setTimeout(() => setMySelf(true), 500);
  }
  useEffect(() => {
    if (mount) {
      if (mount) {
        if (self) {
          setChecked(seasonCheck);
        }
      }
      // eslint-disable-next-line
      return () => (mount = false);
    }
  }, [mount, seasonCheck]);
  useEffect(() => {
    if (mount) {
      if (mount) {
        let isThereSeason = false;
        cartList.map((cart) => {
          if (cart.id === movieId) {
            cart.ep.map((i) => {
              if (i.id === info.id) {
                changeMySelf();
                isThereSeason = true;
              }
            });
          }
        });
        if (!isThereSeason) {
          removeSeason();
        }
      }
      // eslint-disable-next-line
      return () => (mount = false);
    }
  }, [mount]);
  function change() {
    if (seasonCheck && checked) {
      setSeasonCheck(false);
    }
    setChecked(!checked);
  }
  const strId = `checkbox-${id + Math.random()}`;
  return (
    <div className="item episode" style={theme.item}>
      <label
        className="mdl-checkbox mdl-js-checkbox mdl-js-ripple-effect"
        htmlFor={strId}
      >
        <input
          type="checkbox"
          id={strId}
          className="mdl-checkbox__input"
          checked={checked}
          onChange={() => change()}
        />{" "}
        <span className="title">
          e<span>pisode</span> {id}
        </span>
        <span className="prices">{info.price} ETB</span>
        <span className="sizes">{formatSize(info.size)}</span>
      </label>
    </div>
  );
}
