import React, { useContext, useState } from "react";
//style
import "./Option.css";
//context
import { routeContext } from "./../../../Context/routeContext";
import themeContext from "../../../Context/themeContext";
//
function Option({ notifyCount,webView }) {
  const { changeRoute } = useContext(routeContext);
  const { theme, setTheme } = useContext(themeContext);
  //

  //
  const [dark, setDark] = useState(
    localStorage.theme ? localStorage.theme === "dark" : false
  );
  const hideDrawer = (e) => {
    if (e.nativeEvent.path) {
      let path = e.nativeEvent.path.length;
      e.nativeEvent.path[path - 8].className = "mdl-layout__drawer";
      e.nativeEvent.path[path - 6].children[0].children[4].className =
        "mdl-layout__obfuscator";
    } else {
      document.getElementsByClassName("mdl-layout__drawer")[0].className =
        "mdl-layout__drawer";
      document.getElementsByClassName("mdl-layout__obfuscator")[0].className =
        "mdl-layout__obfuscator";
    }
  };
  function changeTheme() {
    setDark(!dark);
    dark ? setTheme("light") : setTheme("dark");
  }
  function redirect(e, value) {
    hideDrawer(e);
    changeRoute(value);
  }
  const linkTo = link =>{
    if(webView){
      if(window.ReactNativeWebView){
        window.ReactNativeWebView.postMessage(JSON.stringify({ link: true, value: link}))
      }
    }else{
      window.location.assign(link)
    }
  }
  const notifyAmount =
    notifyCount - (localStorage.notifyCount ? localStorage.notifyCount : 0);
  return (
    <nav className="mdl-list option">
      <div
        className="mdl-list__item"
        onClick={(e) => redirect(e, "search")}
        style={theme.secBtn}
      >
        <span className="mdl-list__item-primary-content">
          <i className="material-icons mdl-list__item-icon">search</i>
          Search
        </span>
      </div>

      <div
        className="mdl-list__item"
        // onClick={() => changeTheme()}
        style={theme.secBtn}
      >
        <div className="fake-click-area" onClick={() => changeTheme()}></div>
        <span className="mdl-list__item-primary-content">
          <i className="material-icons mdl-list__item-icon">brush</i>
          Dark Theme
        </span>
        <span className="mdl-list__item-secondary-content">
          <label
            className={`mdl-switch mdl-js-switch mdl-js-ripple-effect ${dark ? "is-checked" : ""
              }`}
            htmlFor="dark-switch-1"
          >
            <input
              type="checkbox"
              id="dark-switch-1"
              className="mdl-switch__input hide"
              checked={dark}
              onChange={() => changeTheme()}
            />
          </label>
        </span>
      </div>
      <div
        className="mdl-list__item"
        onClick={(e) => {
          localStorage.removeItem('store')
          window.location.assign(window.location.pathname);
        }}
        style={theme.secBtn}
      >
        <span className="mdl-list__item-primary-content">
          <i className="material-icons mdl-list__item-icon">stores</i>
          Stores
        </span>
      </div>
      <div
        className="mdl-list__item"
        style={theme.secBtn}
        onClick={(e) => redirect(e, "box office")}
      >
        <span className="mdl-list__item-primary-content">
          <i className="material-icons mdl-list__item-icon">theaters</i>
          Box Office
        </span>
      </div>
      <div
        className="mdl-list__item"
        style={theme.secBtn}
        onClick={(e) => redirect(e, "Notifications")}
      >
        <span className="mdl-list__item-primary-content">
          <i
            className={`material-icons mdl-list__item-icon ${notifyAmount > 0 && "mdl-badge mdl-badge--overlap"
              }`}
            data-badge={isNaN(notifyAmount) ? 0 : notifyAmount}
          >
            notifications
          </i>
          Notifications
        </span>
      </div>
      <div
        className="mdl-list__item"
        style={theme.secBtn}
        onClick={(e) => redirect(e, "more apps")}
      >
        <span className="mdl-list__item-primary-content">
          <i className="material-icons mdl-list__item-icon">android</i>
          More Apps
        </span>
      </div>
      <div className="mdl-list__item" style={theme.secBtn} onClick={()=>linkTo('https://t.me/Keni99')}>
        <span className="mdl-list__item-primary-content">
          <i className="material-icons mdl-list__item-icon">feedback</i>
            Feedback
          </span>
      </div>
      <div className="mdl-list__item" style={theme.secBtn} onClick={()=>linkTo(JSON.parse(localStorage.store).help)}>
        <span className="mdl-list__item-primary-content">
          <i className="material-icons mdl-list__item-icon">help</i>
            Help
          </span>
      </div>
    </nav>
  );
}

export default Option;
