import React, { useRef, useContext } from "react";
//style
import "./Tab.css";
//context
import tabContext from "./../../../Context/tabContext";
import themeContext from "../../../Context/themeContext";
//
function Tab({ eye }) {
  const { setTab } = useContext(tabContext);
  const {theme} = useContext(themeContext);
  //
  const tabOne = useRef(null);
  const tabTwo = useRef(null);
  const tabThree = useRef(null);
  const tabFour = useRef(null);
  const activeTab = (e, r) => {
    setTab(r);
    tabOne.current.className = "mdl-layout__tab";
    tabTwo.current.className = "mdl-layout__tab";
    tabThree.current.className = "mdl-layout__tab";
    tabFour.current.className = "mdl-layout__tab";
    e.nativeEvent.srcElement.parentElement.className = "mdl-layout__tab is-active";
  };
  return (
    <React.Fragment>
      <div
        className={`mdl-layout__tab-bar mdl-js-ripple-effect ${
          eye ? "" : "hide"
        } mdl-layout__tab-manual-switch`}
        style={theme.header}
      >
        <div
          className="mdl-layout__tab is-active"
          ref={tabOne}
          onClick={e => activeTab(e, 1)}
        >
          New
        </div>
        <div
          className="mdl-layout__tab"
          ref={tabTwo}
          onClick={e => activeTab(e, 2)}
        >
          Rated
        </div>
        <div
          className="mdl-layout__tab"
          ref={tabThree}
          onClick={e => activeTab(e, 3)}
        >
          Popular
        </div>
        <div
          className="mdl-layout__tab"
          ref={tabFour}
          onClick={e => activeTab(e, 4)}
        >
          Categories
        </div>
      </div>
    </React.Fragment>
  );
}

export default Tab;
