import React, { useState, useContext, lazy, Suspense, useEffect, useRef } from "react";
//style
import "./MovieDetail.css";
//context
import movieContext from "./../../../Context/movieContext";
import cartContext from "./../../../Context/cartContext";
import requestContext from "./../../../Context/requestContext";
import tabContext from "../../../Context/tabContext";
import DetailRenderer from "./DetailRenderer";
import themeContext from "../../../Context/themeContext";
import CardSeriesDetail from "../CardsComonent/Card/CardSeriesDetail/CardSeriesDetail";

//
const Img = lazy(() => import("./Img"));
//
function MovieDetail({ headerDismiss }) {
  const { movie, setMovie } = useContext(movieContext);
  const { setRequest } = useContext(requestContext);
  const { cartList, changeCartList } = useContext(cartContext);
  const { theme } = useContext(themeContext);
  //
  const { tab } = useContext(tabContext);
  const [addCart, setAddCart] = useState(true);
  const [disablePag, setDisablePag] = useState(false);
  const [animate, setAnimate] = useState(false);
  const [direction, setDirection] = useState(false);
  const [ShowState, setShowState] = useState(false);
  const [choose, setChoose] = useState(null);
  const [swipeTrigger, setSwipeTrigger] = useState(false);
  const posterSwipe = useRef(null);

  let mount = true;

  useEffect(() => {
    if (mount) {
      if (swipeTrigger) {
        swipe(swipeTrigger);
        setTimeout(() => {
          setSwipeTrigger(false);
        }, 100);
      }
    }
    // eslint-disable-next-line
  }, [mount, swipeTrigger]);
  useEffect(() => {
    if (mount) {
      let startTouch = 0;
      if (posterSwipe.current) {
        posterSwipe.current.addEventListener('touchstart', e => {
          startTouch = { x: e.changedTouches[0].clientX, y: e.changedTouches[0].clientY };
        });
        posterSwipe.current.addEventListener('touchend', e => {
          let dir = e.changedTouches[0].clientX - startTouch.x;
          let scroll = Math.abs(e.changedTouches[0].clientY - startTouch.y);
          if (scroll < 50) {
            if (dir > 60) {
              setSwipeTrigger('left');
            } else if (dir < -60) {
              setSwipeTrigger('right')
            }
          }
        });
      }
    }
  }, [mount, posterSwipe]);
  if (movie) {
    cartList.map((item) => {
      if (item.id === movie.id) {
        if (addCart) {
          setAddCart(false);
          return false;
        }
      }
      return true;
    });
  }

  useEffect(() => {
    if (mount) {
      if (mount) {
        if (headerDismiss && ShowState) {
          setShowState(false);
        }
      }

      // eslint-disable-next-line
        return () => (mount = false);
    }
  }, [mount, headerDismiss]);
  useEffect(() => {
    if (mount) {
      if (mount) {
        if (!disablePag && direction) {
          setAnimate(direction);
          setTimeout(() => setAnimate(false), 500);
        }
      }

      // eslint-disable-next-line
        return () => (mount = false);
    }
  }, [mount, movie && movie.id]);
  function go(dir) {
    setDirection(dir);
  }
  function swipe(dir) {
    go(dir);
    if (dir === 'left') {
      if (disablePag.side === "right") {
        setDisablePag(false);
      }
    } else {
      if (disablePag.side === "left") {
        setDisablePag(false);
      }
    }
    if (disablePag.side !== dir) {
      if (movie) {
        setRequest(
          "swipeMovie",
          {},
          { side: dir, id: movie.id, key: movie.key }
        ).then((e) => {
          if (e && !e.error) {
            setMovie(e);
            setAddCart(true);
          }
          if (e.error) {
            setDisablePag({ class: `disable-${dir}`, side: dir });
          }
        });
      }
    }
  }
  const sections = [
    "title",
    "Plot",
    "Ratings",
    "runtime",
    "Genre",
    "released",
    "Production",
    "Director",
    "Actors",
    "Writer",
    "Language",
    "country",
  ];
  useEffect(() => {
    if (mount) {
      if (mount) {
        if (!ShowState) {
          setChoose(false);
        }
      }

      // eslint-disable-next-line
        return () => (mount = false);
    }
  }, [mount, ShowState]);
  const show = () => {
    if (movie.single === "0") {
      setShowState(true);
      setRequest("getSeasons", false, movie.id).then((e) => {
        if (e) {
          setChoose(
            <CardSeriesDetail
              item={{ movie: { id: movie.id, title: movie.Title }, seasons: e }}
              pullUp={true}
              setShowState={setShowState}
              disableMoreDetails={true}
            />
          );
        }
      });
    }
  };
  return (
    <div className={`movie-d ${animate}`} ref={posterSwipe}>
      {movie ? (
        <div>
          <div className="poster" style={theme.imgBg} >
            <div className="waiting">
              <i className="material-icons">access_time</i>
            </div>
            <Suspense fallback={<div></div>}>
              <Img id={movie.id} />
            </Suspense>
          </div>
          {tab !== 4 ? (
            <div className={`pag ${disablePag ? disablePag.class : ""}`}>
              <button
                className="back"
                onClick={() => swipe("left")}
                style={theme.secBtnWithBorder}
              >
                <i className="material-icons">keyboard_arrow_left</i>
              </button>
              <button
                className="forward"
                onClick={() => swipe("right")}
                style={theme.secBtnWithBorder}
              >
                <i className="material-icons">keyboard_arrow_right</i>
              </button>
            </div>
          ) : (
              <div />
            )}

          <div className={`details ${animate}`}>
            <span className="mdl-chip center" style={theme.itemLite}>
              <span className="mdl-chip__text">
                {movie.Production ?
                  movie.Production.split(",")
                    ? movie.Production.split(",")[0]
                    : movie.Production
                  : 'N/A'}
              </span>
            </span>
            <div className="add-to-cart">
              {addCart ? (
                <button
                  className={`mdl-button mdl-js-button mdl-js-ripple-effect mdl-button--raised mdl-button--accent ${movie.single === "0" && "make-yellow"
                    }`}
                  onClick={() => {
                    if (movie.single === "1") {
                      changeCartList(
                        {
                          id: movie.id,
                          name: movie.Title,
                          price: movie.price,
                          size: movie.size,
                        },
                        "",
                        true
                      );
                    } else {
                      show();
                    }
                  }}
                >
                  {movie.single === "1" ? (
                    <React.Fragment>
                      <i className="material-icons">add_shopping_cart</i>
                      <span>Add To Cart</span>
                    </React.Fragment>
                  ) : (
                      <React.Fragment>
                        <span>select episodes</span>
                      </React.Fragment>
                    )}
                </button>
              ) : (
                  <button
                    className="red mdl-button mdl-js-button mdl-js-ripple-effect mdl-button--raised mdl-button--accent"
                    onClick={() => {
                      changeCartList(movie.id, "remove");
                      setAddCart(true);
                    }}
                  >
                    <i className="material-icons">clear</i>
                    <span>Remove</span>
                  </button>
                )}
            </div>
            <DetailRenderer movie={movie} Input={false} sections={sections} />
          </div>
        </div>
      ) : (
          ""
        )}
      {ShowState ? choose : ""}
    </div>
  );
}

export default MovieDetail;
