import React, { useContext, useRef, useEffect, useState } from "react";
import "./Download.css";
import { routeContext } from "../../../Context/routeContext";
import requestContext from "../../../Context/requestContext";
import { formatSize } from "../../../Function";

function Download() {
  const { changeRoute } = useContext(routeContext);
  const { setRequest } = useContext(requestContext);
  const [allowed, setAllowed] = useState(false);
  const [data, setData] = useState(false);
  const [error, setError] = useState(false);
  const [url, setUrl] = useState(false);
  const donka = useRef(null);
  let mount = true;
  useEffect(() => {
    if (mount) {
      if (donka) {
        donka.current.parentElement.parentElement.parentElement.className =
          "mdl-layout__content float";
      }
      setRequest("downloadInfo", false).then((e) => {
        if (e) {
          if (mount) {
            setData(e);
          }
        }
      });

      // eslint-disable-next-line
      return () => (mount = false);
    }
  }, [mount]);
  const getBackStupid = () => {
    changeRoute("", true);
    donka.current.parentElement.parentElement.parentElement.className =
      "mdl-layout__content";
  };

  const handleStartDownload = () => {
    setRequest('downloadAvailability', false).then(e => {
      if (e) {
        switch (e.res) {
          case 'ready':
            const a = document.createElement('a');
            a.href = e.url;
            a.setAttribute('download', true);
            setAllowed('zipper');
            setUrl(e.url);
            a.click();
            break;
          case 'failed':
            setAllowed('error');
            setError(JSON.parse(e.missing));
            break;
          case 'unauthorized':
            setAllowed('failed');
            break;
          case 'processing':
            setAllowed('proceed')
            setTimeout(() => handleStartDownload(), 5000);
            break;
          default:
        }
      }
    })
  }
  const modal = (element) => (
    <div className="modal">
      <div className="con">
        {element}
      </div>
    </div>
  );
  const Message = () => {
    switch (allowed) {
      case 'proceed':
        return (
          modal(<div>
            <i className="material-icons">access_time</i>
            <p>Wait! processing  files ...</p>
          </div>)
        )
      case 'error':
        return (
          modal(<div>
            <i className="material-icons">error</i>
            <p>Sorry an error occurred! please contact admin?!</p>
            <button className="mdl-button" onClick={() => setAllowed(false)}>ok</button>
          </div>)
        )
      case 'failed':
        return (
          modal(<div className='failed'>
            <i className="material-icons">payment</i>
            <p>You have to pay first to download the items!!</p>
            <button className="mdl-button" onClick={() => setAllowed(false)}>ok</button>
          </div>)
        )
      case 'zipper':
        return (
          modal(<div>
            <h4>Thank you, bye</h4>
                If you can't unzip the file you can download(free):
                <button className="mdl-button mdl-js-button go-back-btn" onClick={() => window.location.assign(window.location.pathname)}>
              <i className="material-icons">arrow_back</i> Go back
            </button>
            <ul>
              <a href="/zipper?p=windows">
                <li>winRar for windows</li>
              </a>
              <a href="/zipper?p=android">
                <li>zArchiver for android</li>
              </a>
              <br />
              <br />
              <p>If the download didn't start click,<a href={url ? url : undefined}>here</a></p>
            </ul>
          </div>)
        )
      default:
    }
  }
  return (
    <div className="download" ref={donka}>
      <div className="con">
        <h3>Download</h3>
        <button className="mdl-button mdl-button--raised start-download" onClick={handleStartDownload}>
          <i className="material-icons">file_download</i>
          Start Download
        </button>
        <div className="info-text">
          Tell The Admin Your Id and pay the price listed below, after that press "start download" to continue.
        </div>

        {allowed && <Message />}

        {data ? (
          <div>
            <div className="id">#{data.id}</div>
            <div className="worth">
              <span className="break">
                <div className="price carish">
                  <span className="title">Price</span>
                  <span className="val">{data.price}</span>
                  <span className="disk">ETB</span>
                </div>
                <div className="size carish">
                  <span className="title">Size</span>
                  <span className="val">{formatSize(data.size, true)}</span>
                  <span className="disk">
                    {formatSize(data.size, false, true)}
                  </span>
                </div>
              </span>
              <div className="info">
                <table className="mdl-data-table mdl-js-data-table">
                  <thead>
                    <tr>
                      <th className="mdl-data-table__cell--non-numeric">
                        Configuration
                      </th>
                      <th>Value</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td className="mdl-data-table__cell--non-numeric">
                        Subtitles
                      </td>
                      <td>{data.config.sub ? "Yes" : "no"}</td>
                    </tr>
                    <tr>
                      <td className="mdl-data-table__cell--non-numeric">
                        Languge
                      </td>
                      <td>{data.config.languge}</td>
                    </tr>
                    <tr>
                      <td className="mdl-data-table__cell--non-numeric">
                        Quality
                      </td>
                      <td>{data.config.quality}p</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>

            <div className="list">
              <div className="head">
                Cart
                <span>
                  <button
                    className="mdl-button mdl-js-button"
                    onClick={getBackStupid}
                  >
                    <i className="material-icons">edit</i> Edit
                  </button>
                </span>
              </div>
              <div className="cart">
                <div className="cart-list">
                  {data.cart[0]
                    ? data.cart.map((item, key) => (
                      <div className={`item ${error ? error.includes(item.id.toString()) && 'red' : ''}`} key={key}>
                        <span className="num">{key + 1}. </span>
                        <span className="name">
                          {item.name}{" "}
                          {item.ep ? "(" + item.ep.length + "e)" : ""}
                        </span>
                        <span className="price">{item.price} ETB</span>
                        <span className="size">{formatSize(item.size)}</span>
                      </div>
                    ))
                    : ""}
                </div>
              </div>
            </div>
          </div>
        ) : (
          ""
        )}
      </div>
    </div >
  );
}
export default Download;
