import React from 'react'
import {dark} from '../Theme/Dark'

function startTheme(){
    const theme = localStorage.theme?localStorage.theme:''
    if(theme === 'dark'){
      return dark;
    }
    return {};
}
export const darkTheme = dark;
export const defaultTheme = startTheme();
export default React.createContext({
  theme: {},
  setTheme:()=>{}
});