/* eslint-disable array-callback-return */
import React, { useContext } from "react";
import themeContext from "../../../Context/themeContext";

function Title({ title, movie, Input }) {
  const { theme } = useContext(themeContext);
  return (
    <React.Fragment>
      <li className="mdl-list__item">
        <span
          className="mdl-list__item-primary-content uppercase"
          style={theme.secBtn}
        >
          {Input ? <Input name="Title" value={title} /> : title}
        </span>
        <span className="mdl-list__item-secondary-action rated">
          {Input ? <Input name="Rated" value={movie.Rated} /> : movie.Rated}
        </span>
      </li>
    </React.Fragment>
  );
}
function Rating({ rating, Input }) {
  const { theme } = useContext(themeContext);

  return (
    <React.Fragment>
      {rating.map((rate, i) => (
        <li className="mdl-list__item" key={i}>
          <span className="mdl-list__item-primary-content" style={theme.secBtn}>
            {rate.Source}
          </span>
          <span
            className="mdl-list__item-secondary-action rating"
            style={theme.secBtn}
          >
            {Input ? (
              <Input name={"Ratings%" + i} value={rate.Value} />
            ) : (
              rate.Value
            )}
          </span>
        </li>
      ))}
    </React.Fragment>
  );
}
function List({ name, Input, x }) {
  const { theme } = useContext(themeContext);

  name = name.toString().split(",");
  return (
    <React.Fragment>
      {name.map((i, key) => (
        <li key={key} className="mdl-list__item">
          <span className="mdl-list__item-primary-content" style={theme.secBtn}>
            <i className="material-icons">keyboard_arrow_right</i>
            {Input ? <Input name={x + "%" + key} value={i} /> : i}
          </span>
        </li>
      ))}
    </React.Fragment>
  );
}
function Mapper({ movie, x, item, isRating, isTitle, Input }) {
  const { theme } = useContext(themeContext);
  return (
    <div className="item" style={!Input ? theme.itemLite : { zIndex: 1 }}>
      <div className="header" style={theme.borderBottom}>
        <h5>{x} </h5>
      </div>
      <div className="list">
        <ul className="mdl-list" style={{ margin: 0 }}>
          {isTitle ? (
            <Title title={item} movie={movie} Input={Input} />
          ) : isRating ? (
            <Rating rating={item} Input={Input} />
          ) : JSON.stringify(item) ? (
            JSON.stringify(item).search(",") + 1 && x !== "Plot" && !Input ? (
              <List name={item} x={x} Input={Input} />
            ) : (
              <li className="mdl-list__item">
                <span
                  className="mdl-list__item-primary-content"
                  style={theme.secBtn}
                >
                  {Input ? <Input name={x} value={item} /> : item}
                  {/* {movie.Title} */}
                </span>
              </li>
            )
          ) : (
            ""
          )}
        </ul>
      </div>
    </div>
  );
}

function DetailRenderer({ movie, Input, sections }) {
  let result = [];
  function checkValid(name) {
    let valid = false;
    sections.map((i) => {
      if (i.toLowerCase() === name.toLowerCase()) {
        valid = true;
      }
    });
    return valid;
  }
  function getOrder(name) {
    let order = 0;
    sections.map((i, key) => {
      if (i.toLowerCase() === name.toLowerCase()) {
        order = key;
      }
    });
    return order;
  }
  for (var x in movie) {
    if (checkValid(x)) {
      result.push(
        <Mapper
          key={x}
          order={getOrder(x)}
          movie={movie}
          x={x}
          item={movie[x]}
          isRating={x.toLowerCase() === "ratings"}
          isTitle={x.toLowerCase() === "title"}
          Input={Input}
        />
      );
    }
  }
  result.sort(function (x, y) {
    return x.props.order - y.props.order;
  });
  return (
    <div>
      {result.map((i, key) => (
        <div key={key}>{i}</div>
      ))}
    </div>
  );
}
export default DetailRenderer;
