/* eslint-disable react-hooks/exhaustive-deps */
import React, { useRef, useEffect, useContext, useState } from "react";

import "./SearchBox.css";
import searchContext from "./../../../Context/searchContext";
import requestContext from "./../../../Context/requestContext";
import themeContext from "../../../Context/themeContext";
//sug === Suggestions
function SearchBox({ dismiss }) {
  const searchInput = useRef(null);
  const [input, setInput] = useState("");
  const { setSearchResponse } = useContext(searchContext);
  const { setRequest } = useContext(requestContext);
  const { theme } = useContext(themeContext);
  //
  const [sug, setSug] = useState([]);
  const [requestTime, setRequestTime] = useState(false);
  let mount = true;

  useEffect(() => {
    if (mount) {
      searchInput.current.focus();
    }
    // eslint-disable-next-line
        return () => (mount = false);
  }, [mount, searchInput]);
useEffect(() => {
  if (mount) {
    if (mount) {
      if (dismiss) {
        setSug([]);
      }
    }
    // eslint-disable-next-line
        return () => (mount = false);
  }
}, [mount, dismiss]);
const getSug = () => {
  if (input !== "") {
    setRequest("inlineSug", false, input).then((res) => {
      if (res) {
        setSug(res);
      }
    });
  }
};
const handleInputChange = (e) => {
  setInput(e.target.value);
  let time = new Date().getTime();
  if (time - requestTime > 750) {
    getSug();
    setRequestTime(time);
  }
};
const getResult = () => {
  if (input !== "") {
    setRequest("search", false, input).then((e) => {
      if (e) {
        setSearchResponse(e);
        searchInput.current.blur();
        setSug([]);
      } else {
        setTimeout(getResult, 3000);
      }
    });
  }
};
const handleKeyPress = (e) => {
  if (e.keyCode === 13) {
    if (input !== "") {
      getResult();
    }
  }
};
const getResultSug = (item) => {
  setSug(false);
  setInput(item);
  setRequest("search", false, item).then((e) => {
    if (e) {
      setSearchResponse(e);
      searchInput.current.blur();
      setSug([]);
    } else {
      setTimeout(getResultSug, 3000);
    }
  });
};
return (
  <div className="search-box">
    <input
      className="input"
      type="text"
      placeholder="Search Your Movie"
      ref={searchInput}
      value={input}
      onChange={handleInputChange}
      onKeyUp={handleKeyPress}
    />
    <button
      className="mdl-button mdl-js-button mdl-button--icon btn"
      onClick={() => getResult()}
    >
      <i className="material-icons">search</i>
    </button>
    <div
      className={`sug ${sug[0] ? "" : "hide"} ${input !== "" ? "" : "hide"}`}
      style={theme.itemLiteDarker}
    >
      <div className="head">Suggestions</div>
      {sug[0]
        ? input !== ""
          ? sug.map((item, key) => (
            <div
              className="item"
              key={key}
              onClick={() => getResultSug(item.title)}
              style={theme.borderBottom}
            >
              {item.title}
            </div>
          ))
          : ""
        : ""}
    </div>
  </div>
);
}
export default SearchBox;
