/* eslint-disable react-hooks/exhaustive-deps */
import React, { useContext, useEffect, useState } from "react";
import themeContext from "../../../../Context/themeContext";
//style
import "./Card.css";
// component
import CardBox from "./CardBox";
//
export default ({ item, order, dismiss, headerDismiss, highLightFor }) => {
  const [cardState, setCardState] = useState(false);
  const { theme } = useContext(themeContext);
  let mount = true;

  useEffect(() => { if (mount){
    if (mount) {
      if (headerDismiss && cardState) {
        setCardState(false);
      }
    }
    // eslint-disable-next-line
        return () => (mount = false);
  }}, [mount,headerDismiss]);
  useEffect(() => { if (mount){
    if (mount) {
      if (cardState && Number(item.movie.single)) {
        setTimeout(() => {
          setCardState(false);
        }, 10000);
      }
    }
    // eslint-disable-next-line
        return () => (mount = false);
  }}, [mount,cardState]);
  return (
    <div className="col phone-1 tab-1 desk-1">
      <div
        className={`card ${Number(item.movie.single) ? "" : "multi"}`}
        id={`c${order}`}
        onClick={() => {
          if (cardState) {
            if (Number(item.movie.single)) {
              setCardState(false);
            }
          } else {
            setCardState(true);
          }
        }}
        style={theme.item}
      >
        <CardBox
          item={item}
          cardState={cardState}
          setCardState={setCardState}
          highLightFor={highLightFor}
        />
      </div>
    </div>
  );
};

